import { gql } from "@apollo/client";

export const LOGIN_USER = gql`
  mutation loginUser($username: String!, $password: String!) {
    loginUser(username: $username, password: $password){
      token
      user{
        id
        email
        username
        lastName
        firstName
        image
        roles{
          id
          role
        }
        pages{
          id
          page
        }
        tel
      }
    }
    
  }
`;

export const PROFILE = gql`
  query Profile {
    currentUser @client
  }
`;

export const GET_ROLES = gql`
  query getRoles {
    roles @client
  }
`;


export const REGISTER_USER = gql`
mutation registerUser($username:String!, $password:String!,$firstname:String!,$lastname:String!,$email:String!,$role:String!,$tel:String!,$page:String){
  registerUser(newUser:{
    username:$username
    password:$password
    firstName:$firstname
    lastName:$lastname
    email:$email
    role:$role
    tel:$tel
    page:$page
  }){
    success
    message
  }
}

`;

export const GET_ALL_USERS = gql `
query getUserWithPagination($page:Int,$limit:Int,$keyword:String){
  getUserWithPagination(page:$page,limit:$limit,keyword:$keyword){
  	users{
      id
      username
      lastName
      firstName
      email
    	roles{
        id
        role
      }
      pages{
        id
        page
      }
      tel
    }
    paginator{
      slNo
      prev
      next
      perPage
      totalPosts
      totalPages
      currentPage
      hasPrevPage
      hasNextPage
      totalDocs
    }
  }
}
`;

export const UPDATE_USER_DETAIL = gql`
mutation updateUserDetail($userId:ID!,$firstName:String!,$lastName:String!,$email:String!,$tel:String!){
  updateUserDetail(
    userId:$userId,
    tel:$tel,
    firstName:$firstName,
    lastName:$lastName,
    email:$email,
    
    ){
    success
    message
  }
}
`;

export const DELETE_USER = gql`
mutation deleteUser($userId:ID!){
  deleteUser(userId:$userId){
    success
    message
  }
}
`;

export const ADD_USER_ROLE = gql`
mutation addRole($userId:ID!,$role:String!){
  addRole(userId:$userId,role:$role){
    success
    message
  }
}
`;

export const DELETE_USER_ROLE = gql`
mutation deleteRole($userId:ID!,$roleId:ID!){
  deleteRole(userId:$userId,roleId:$roleId){
    message
    success
  }
}
`
export const ADD_USER_PAGE = gql`
mutation addPage($userId:ID!,$page:String!){
  addPage(userId:$userId,page:$page){
    message
    success
  }
}
`

export const DELETE_USER_PAGE = gql`
mutation deletePages($userId:ID!,$pageId:ID!){
  deletePages(userId:$userId,pageId:$pageId){
    message
    success
  }
}
`

export const USER_PROFILE =gql`
query getCurrentUser{
  getCurrentUser{
    id
    email
    username
    lastName
    firstName
    image
    roles{
      id
      role
    }
    pages{
      id
      page
    }
    tel
  }
}
`

