import React, {useEffect, useState} from 'react'
import { Table, Modal, Row, Col, Button, Select, Divider, Form, Input, DatePicker, message} from 'antd'
import { relatedCol } from '../tableColumn/relatedColumn'
import { ListSelect } from '../../../static/own-comp'
import { useMutation, useQuery } from '@apollo/client'
import { convertToCommune, convertToDistrict, convertToVillage, convertPersonalToSelect } from '../../../function/fn'
import { provinceData, districtData, communeData, villageData, genderData, nationalityData } from '../../../context/headerContext'
import { CREATE_NEW_PERSON, GET_AFFECTEDFROM_BY_PATIENT, GET_ALL_PERSONINFO_NEGATIVE, UPDATE_AFFECTEDFROM } from '../../../graphql/people'
import moment from 'moment'
import { CREATE_PERSON_QUARANTINE } from '../../../graphql/quarantine'
const {Option} = Select

export default function QuarantineRelated({open, setOpen,data, setRefetch,setRefetchAff ,fullName, type, roomNumber}) {
    const {data:personalInfo} = useQuery(GET_ALL_PERSONINFO_NEGATIVE)
    const personalInfoData = personalInfo?.allPersonalInfosForThatNegative;
    let [form] = Form.useForm()

    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")
    const [personalInfoIdRelated, setPersonalInfoIdRelated] = useState("")

    const [affectedData,setAffectedData] = useState([])

    const { data:affectedD, refetch:refetchAffected } = useQuery(GET_AFFECTEDFROM_BY_PATIENT, {
        variables: {
            page: 1,
            limit: 100,
            keyword: "",
            patientId: data.peopleId
        },
        onCompleted: ({ getAffectedPersonalListWithPagination }) => {

            setAffectedData(getAffectedPersonalListWithPagination)
        },
        fetchPolicy: 'network-only',
        nextFetchPolicy:"cache-only"
    })

    useEffect(()=>{
        if(affectedD!==undefined){
            let affectedFiltered = affectedD?.getAffectedPersonalListWithPagination?.personalInfos?.filter(e => e.affectedFrom.relationType === type)
            setAffectedData(affectedFiltered)

        }
    },[affectedD,open])

    const [createPersonalInfo, { loading: loadingCreate, error: errorCreate }] = useMutation(CREATE_NEW_PERSON, {
        onCompleted: ({ createPersonalInfo }) => {
            // if (personalInfoId === "new") {
            //     if(createPersonalInfo.response.success=== false){
            //         setPeopleSampleFn()
            //         message.error(createPersonalInfo.response.message)
            //     }else{                   
            //         setPersonalInfoId(createPersonalInfo.personalInfo)
            //         message.success("!")
            //     }
            // } else {
                if(createPersonalInfo.response.success=== false){
                    message.error(createPersonalInfo.response.message)
                }else{                   
                    refetchAffected()
                    setRefetch()
                    setRefetchAff()
                    message.success("!")
                }
                
            // }
        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const [updateAffectedFrom, { loading: loadingUpdate, error: errorUpdate }] = useMutation(UPDATE_AFFECTEDFROM, {
        onCompleted: ({ updateAffectedFrom }) => {
            if(updateAffectedFrom.success=== false){
                message.error(updateAffectedFrom.response.message)
            }else{                   
                refetchAffected()
                setRefetch()
                setRefetchAff()
                message.success("!")
            }
        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const [addPeopleToQuarantine]=useMutation(CREATE_PERSON_QUARANTINE,{
        onCompleted:({addPeopleToQuarantine})=>{
            if(addPeopleToQuarantine.success===true){
                setRefetch()
                refetchAffected()
                setRefetchAff()
                message.success("")
            }else{
                message.error(addPeopleToQuarantine.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })


    const onFinish = (values) => {
        if(personalInfoIdRelated === "new"){

            createPersonalInfo({variables: {
                ...values, 
                other: values.other === undefined ? null : values.other, 
                otherAffect: values.otherAffect === undefined ? null : values.otherAffect, 
                age: parseInt(values.age), 
                times: parseInt(values.times),
                village: values.village === undefined ? "" : values.village, 
                district: values.district === undefined ? "" : values.district, 
                commune: values.commune === undefined ? "" : values.commune,
                patientName:fullName,
                patientCode:data.peopleId,
                affectedDate: values.affectedDate === null || values.affectedDate === undefined ? null : moment(values.affectedDate),
                lastTouchAt: values.lastTouchAt === null || values.lastTouchAt === undefined ? null : moment(values.lastTouchAt),
            }})

            var array = {
                ...values,
                // description: values.description === undefined ? null : values.description,
                date_in: moment(values.date_in).format(),
                date_out: values.date_out === undefined || values.date_out === null ? null : moment(values.date_out).format(),
                personalInfo:values.personalInfo,
                quarantineInfo:data.quaranting.quarantineInfo,
                locationName: data.quarantining.locationName,
                long:  parseFloat(data.quarantining.long),
                lat: parseFloat(data.quarantining.lat),
                // locationType:quarantineData.locationType
            }

        } else {
            let index = personalInfoData.findIndex(ele => ele.id === personalInfoIdRelated)
            
            updateAffectedFrom({
                variables:{
                    ...values, 
                    otherAffect: values.otherAffect === undefined ? null : values.otherAffect, 
                    patientCode: data.peopleId,
                    patientName: fullName,
                    personalInfoId: personalInfoData[index].id,
                    affectedDate: values.affectedDate === null || values.affectedDate === undefined ? null : moment(values.affectedDate),
                    lastTouchAt: values.lastTouchAt === null || values.lastTouchAt === undefined ? null : moment(values.lastTouchAt),
                }
            })

            var array = {
                ...values,
                // description: values.description === undefined ? null : values.description,
                date_in: moment(values.date_in).format(),
                date_out: values.date_out === undefined || values.date_out === null ? null : moment(values.date_out).format(),
                personalInfo:values.personalInfo,
                quarantineInfo:data.quaranting.quarantineInfo,
                locationName: data.quarantining.locationName,
                long:  parseFloat(data.quarantining.long),
                lat: parseFloat(data.quarantining.lat),
                // locationType:quarantineData.locationType
            }
        }

        
        // console.log(values)
        addPeopleToQuarantine({variables:array})

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToGenderFn = (e) => {
        form.setFieldsValue({
            gender: e
        });
    };

    const setToPersonalInfoFn = (e) => {
        form.setFieldsValue({
            personalInfoIdRelated: e,
        });

        setPersonalInfoIdRelated(e);
      };

    const setToNationalityFn = (e) => {
        form.setFieldsValue({
            nationality: e
        });
    };

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };


    const handleRelatedDelete =()=>{

    }

    return (
        <Modal
            width={800}
            visible={open}
            title="?"
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addPeople"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                fields={[
                    {
                        name:"relationType",value:type
                    },
                    {
                        name:"direct",value:true
                    }
                ]}
            >
                <Row>
                    <Col
                        xs={24}
                        md={{ span: 24, offset: 0 }}
                    >
                        <Form.Item
                        name="personalInfoIdRelated"
                        rules={[{ required: true, message: "!" }]}
                        >
                        <ListSelect
                            type={6}
                            data={convertPersonalToSelect(personalInfoData)}
                            title=""
                            setValue={setToPersonalInfoFn}
                        />
                        </Form.Item>
                    </Col>

                    {personalInfoIdRelated === "new" ? (
                        <>
                        <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="idCard"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="patientId"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="lastName"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="firstName"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="englishName"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="dob"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="gender"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <ListSelect type={0} data={genderData} title="" setValue={setToGenderFn} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="age"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" type="number" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="occupation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="tel"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" addonBefore="+855" type="number" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="nationality"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={0} data={nationalityData} title="" setValue={setToNationalityFn} />
                        </Form.Item>
                    </Col>



                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="province"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="/" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>

                    {province === "" ? (
                        <>
                            <Col xs={24} md={{ span: 11 }}>
                                <Form.Item
                                    name="district"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={0} data={convertToDistrict(districtData)} title="/" setValue={setToDistrictFn} disabled={province !== "" ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="commune"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToCommune(district, communeData)} title="/" setValue={setToCommuneFn} disabled={district === "" || district === null ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 24 }}>
                                <Form.Item
                                    name="village"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToVillage(commune, villageData)} title="" setValue={setToVillageFn} disabled={commune === "" || commune === null ? true : false} />
                                </Form.Item>
                            </Col>
                        </>
                    ) : null}

                    {/* <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="relation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}

                    {/* <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vaccinated"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col> */}

                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="chronic"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    
                    <Divider></Divider>
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="date"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="times"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="vaccineType"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="">
                                <Option value="SINOPHARM">SINOPHARM</Option>
                                <Option value="SINOVAC">SINOVAC</Option>
                                <Option value="ASTRAZENECA">ASTRAZENECA</Option>
                                <Option value="JOHNSON JOHNSON">JOHNSON JOHNSON</Option>
                                <Option value="PFIZER">PFIZER</Option>
                                <Option value="Other">Other</Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="vacinatedAt"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                        </>
                    ):null}
                    

                    <Divider></Divider>
                   
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="direct"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="">
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="relationType"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" disabled>
                                    <Option value=""></Option>
                                    <Option value=""></Option>
                                    <Option value=""></Option>
                                    <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="relation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="affectedDate"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width:"100%"}} placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="lastTouchAt"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width:"100%"}} placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="otherAffect"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input.TextArea placeholder="" />
                        </Form.Item>
                    </Col>

                    {/* <Divider></Divider>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="case"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <ListSelect type={4} data={allCases} title="" setValue={setToCaseFn} disabled={caseId === "new" ? true : false} />
                        </Form.Item>
                    </Col>

                    {
                        caseId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="caseName"
                                >
                                    <Input disabled={true} style={{ backgroundColor: "white", color: "black" }} />
                                </Form.Item>
                            </Col>
                        ) : null
                    }

                    <Col xs={24} md={caseId === "new" ? { span: 24 } : { span: 11, offset: 2 }}>
                        <Form.Item
                            name="direct"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>

                            </Select>
                        </Form.Item>
                    </Col>

                    

                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="other"

                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col> */}


                    {/* <Col xs={24}>
                        <Form.Item
                            name="interviewed"
                            valuePropName="checked"
                        >
                            <Checkbox></Checkbox>
                        </Form.Item>
                    </Col> */}


                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ 
                                width: "100%",
                            }}
                        >
                            
                        </Button>
                    </Col>
                    <Col
                        xs={24}
                        style={{
                            marginTop: 20
                        }}
                    >
                        <Table
                            className="table-personal"
                            columns={relatedCol({ handleRelatedDelete })}
                            dataSource={affectedData}
                            rowKey={record => record.id}
                            pagination={false}
                            // pagination={{
                            //     total: subCasePagination?.totalDocs,
                            //     // showSizeChanger: true,
                            //     onChange: ((page, pageSize) => { setPage(page); setLimit(pageSize) })
                            // }}
                            scroll={{ x: 1000, y: 300 }}
                            sticky
                        />
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
