import React, { useContext, useState } from 'react'
import { Modal, Form, Input, Row, Col, Button, Select, message, Table } from 'antd';
import { useMutation } from '@apollo/client';
import { ADD_USER_PAGE, DELETE_USER_PAGE } from '../../../graphql/auth';
import { roleColumn } from '../tableColumn/roleColumn';
import { pageColumn } from '../tableColumn/pageColumn';

const { Option } = Select;

export default function AddPage({ open, setOpen,userID, dataPage , setRefetch }) {
   
    const [addPage, { loading, error }] = useMutation(ADD_USER_PAGE, {
        onCompleted: ({ addPage }) => {
            if(addPage.success===true){
                setRefetch()
                message.success("")
            }else{
                message.success(addPage.message )
            }
        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const [deletePages, { loading: deleteLoading, error: deleteError }] = useMutation(DELETE_USER_PAGE, {
        onCompleted: ({ deletePages }) => {
            if(deletePages.success===true){
                setRefetch()
                message.success("")
                setOpen(false)
                form.resetFields()
            }else{
                message.error(deletePages.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })


    let [form] = Form.useForm()

    const onFinish = (values) => {
        // console.log('Success:', values);

        addPage({
            variables: {
                userId:userID,
                page:values.page
            }
        })

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const handleDelete = (e) => {
        deletePages({variables:{
            userId:userID,
            pageId:e
        }})
    }

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Form
                form={form}
                name="addPage"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={12}>
                        <Form.Item
                            name="page"
                            rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value="/"></Option>
                                <Option value="/sample"></Option>
                                <Option value="/interview"> COVID-19</Option>
                                <Option value="/hospital"></Option>
                                <Option value="/quarantine"></Option>
                                <Option value="/specifylocation">/</Option>
                                <Option value="/people"></Option>
                                <Option value="/reportDaily"></Option>
                                <Option value="/reportExcel"> Excel</Option>
                                <Option value="/case"></Option>
                                <Option value="/user"></Option>
                            </Select>
                            
                        </Form.Item>
                    </Col>
                    <Col xs={{span: 11, offset:1}}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        // onClick={()=> }
                        >
                            
                        </Button>
                    </Col>

                    <Col xs={24}>
                        <Table
                            columns={pageColumn({handleDelete})}
                            dataSource={dataPage}
                            pagination={false}
                        />
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
