import React from "react";
import { MapContainer as LeafletMap, TileLayer,LayersControl } from "react-leaflet";
import * as L from "leaflet";
import "./Map.css";
import ReactLeafletGoogleLayer from 'react-leaflet-google-layer';
import { showDataOnMap,showQuarantineInfoOnMap ,showHospitalInfoOnMap} from "./util";
import { FaMapMarkedAlt } from 'react-icons/fa';
import {useQuery} from '@apollo/client'
import {ALL_QUARANTINEINFO} from '../../graphql/quarantine'
import QuarantineIcon from '../../asset/quarantineIcon.png'
import HospitalIcon from '../../asset/hospitalIcon.png'


// allHospitalInfos
function CustomMap({ district, center={ lat: 13.3633, lng: 103.8564 }, zoom,data,type }) {
  const [satellite, setSatellite] = React.useState(false);

  const LeafIcon = L.Icon.extend({
    options: {}
  });
  
  const blueIcon = new LeafIcon({
  iconUrl:QuarantineIcon
  }),
  greenIcon = new LeafIcon({
      iconUrl:HospitalIcon
  });


  
  return (
    <div className="map">
        <button className="layerSwitcher"
      onClick={() => setSatellite(!satellite)}
     >
      <FaMapMarkedAlt />

     </button>
      <LeafletMap center={center} zoom={zoom} >
      
 
        <TileLayer
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
    
        {type==="qurantine" && showQuarantineInfoOnMap(data,blueIcon)}
        {type==="hospital" && showHospitalInfoOnMap(data,greenIcon) } 
         {satellite ? (
       <ReactLeafletGoogleLayer
        googleMapsLoaderConf={{
         KEY: 'AIzaSyCLpho9FZOn3PUIt7Pm8R6GPTIQQrJ1_1M',
        }}
        type={'satellite'}
       />
      ) : null}
      </LeafletMap>
    </div>
  );
}

export default CustomMap;
