import React, {useEffect, useState} from 'react'
import { Modal, Row, Col, Timeline, Button } from 'antd'
import Tab1 from '../newForm/tab1'
import Tab2 from '../newForm/tab2'
import Tab3 from '../newForm/tab3'
import Tab4 from '../newForm/tab4'
import Tab5 from '../newForm/tab5'
import Tab6 from '../newForm/tab6'

export default function NewForm({open, setOpen, peopleId, data, setRefetch, setRefetchAffected}) {
    const [openTab1, setOpenTab1] = useState(false)
    const [openTab2, setOpenTab2] = useState(false)
    const [openTab3, setOpenTab3] = useState(false)
    const [openTab4, setOpenTab4] = useState(false)
    const [openTab5, setOpenTab5] = useState(false)
    const [openTab6, setOpenTab6] = useState(false)

    const [tab1Data,setTab1Data] = useState({})
    const [tab2Data,setTab2Data] = useState({})
    const [tab3Data,setTab3Data] = useState({})
    const [tab5Data,setTab5Data] = useState({})
    const [tab4Data,setTab4Data] = useState({})
    const [tab6Data,setTab6Data] = useState({})

    useEffect(()=>{
        if(data!==undefined){

            setTab1Data({
                peopleId:data?.id,
                interviewedAt:data?.interviewedAt,
                recievedLabFormAt:data?.recievedLabFormAt,
                officerId:data?.officerId,
                updateAt:data?.updateAt,
                interviewed : data?.interviewed
            })
        
            setTab2Data({
                peopleId:data?.id,
                firstName:data?.firstName,
                lastName:data?.lastName,
                gender:data?.gender,
                dob:data?.dob,
                nationality:data?.nationality,
                occupation:data?.occupation,
                village:data?.village,
                commune:data?.commune,
                district:data?.district,
                province:data?.province,
                social:data?.social,
                idCard:data?.idCard,
                tel:data?.tel,
                vaccination:data?.vaccination
            })

            let lengthSampleTest = data?.sampleTest?.length
            let sampleTest = data?.sampleTest

            let lengthHospitalization = data?.hospitalizations?.length
            let hospitalization = data?.hospitalizations

            let lengthQuaranting = data?.quaranting?.length

            setTab3Data({
                peopleId:data?.id,
                date: sampleTest[lengthSampleTest-1]?.date,
                reasonForTesting: sampleTest[lengthSampleTest-1]?.reasonForTesting,
                testLocation: sampleTest[lengthSampleTest-1]?.testLocation === "" ? null : sampleTest[lengthSampleTest-1]?.testLocation,
                result: sampleTest[lengthSampleTest-1]?.result,
                resultDate: sampleTest[lengthSampleTest-1]?.resultDate,
                relapse: data?.relapse,
                souceOfSuspect: data?.souceOfSuspect,
                hospitalName: data?.hospitalizations[lengthHospitalization-1]?.hospitalName,
                quarantineName: data?.quaranting[lengthQuaranting-1]?.locationName,
                symptom:sampleTest[lengthSampleTest-1]?.symptom,
                symptomStart: sampleTest[lengthSampleTest-1]?.symptomStart,
                sampleTestId: sampleTest[lengthSampleTest-1]?.id,
                hospitalizations: data?.hospitalizations[lengthHospitalization-1],
                quaranting: data?.quaranting[lengthQuaranting-1]
            })

            setTab5Data({
                ...data?.travelOverCountryHistory,
                peopleId:data?.id,
            })

            setTab6Data({
                peopleId:data?.id,
                chronic:data?.chronic
            })

            setTab4Data({
                peopleId:data?.id,
                currentAddress:data?.currentAddress,
                workplaceInfo:data?.workplaceInfo,
                totalCoworker:data?.totalCoworker,
                carPlateNumber:data?.carPlateNumber,
                driverName:data?.driverName,
                from:data?.from,
                to:data?.to,
                historyWithin14days: data?.historyWithin14days,
                quaranting: data?.quaranting[lengthQuaranting-1],
                quarantineName: data?.quaranting[lengthQuaranting-1]?.locationName,
            })

            // console.log(data)

        }
    },[data])

    return (
        <Modal
            visible={open}
            onCancel={() => setOpen(false)}
            title=""
            footer={null}
        >
            <Tab1 open={openTab1} setOpen={setOpenTab1} data={tab1Data} setRefetch={setRefetch} />
            <Tab2 open={openTab2} setOpen={setOpenTab2} data={tab2Data} setRefetch={setRefetch}/>
            <Tab3 open={openTab3} setOpen={setOpenTab3} data={tab3Data} setRefetch={setRefetch} />
            <Tab4 open={openTab4} setOpen={setOpenTab4} data={tab4Data} setRefetch={setRefetch} fullName={data?.lastName+" "+data?.firstName} setRefetchAffected={setRefetchAffected} />
            <Tab5 open={openTab5} setOpen={setOpenTab5} data={tab5Data} setRefetch={setRefetch}/>
            <Tab6 open={openTab6} setOpen={setOpenTab6} data={tab6Data} setRefetch={setRefetch} />

            <Row>
                <Col
                    xs={24}
                >
                    <Timeline>
                        <Timeline.Item>
                            <span
                                className="link"
                                onClick={() => setOpenTab1(true)}
                            >
                                intro
                            </span>
                        </Timeline.Item>
                        <Timeline.Item>
                            <span
                                className="link"
                                onClick={() => setOpenTab2(true)}
                            >
                                .
                            </span>
                        </Timeline.Item>
                        <Timeline.Item>
                            <span
                                className="link"
                                onClick={() => setOpenTab3(true)}
                            >
                                .
                            </span>
                        </Timeline.Item>
                        <Timeline.Item>
                            <span
                                className="link"
                                onClick={() => setOpenTab4(true)}
                            >
                                .
                            </span>
                        </Timeline.Item>
                        <Timeline.Item>
                            <span
                                className="link"
                                onClick={() => setOpenTab5(true)}
                            >
                                .
                            </span>
                        </Timeline.Item>
                        <Timeline.Item>
                            <span
                                className="link"
                                onClick={() => setOpenTab6(true)}
                            >
                                .
                            </span>
                        </Timeline.Item>
                    </Timeline>
                </Col>
            </Row>
        </Modal>
    )
}
