export function screenResponsive(size,setMeasure,setClass){
    let classSet = ""
    if(setMeasure==="gte"){
        if(window.innerWidth>=size){
            classSet=setClass
        }
    }else if(setMeasure==="lte"){
        if(window.innerWidth<=size){
            classSet=setClass
        }
    }else if(setMeasure==="lt"){
        if(window.innerWidth<size){
            classSet=setClass
        }
    }else if(setMeasure==="gt"){
        if(window.innerWidth>size){
            classSet=setClass
        }
    }
    
    return classSet;
} 

export function convertForPeopleExcel(e){
    let array = []
    e.map(load => {
        array.push({
            date: load.sampleTest.date,
            patientId: load.patientId,
            lastName: load.lastName,
            firstName: load.firstName,
            gender: load.gender,
            age: load.age,
            nationality: load.nationality,
            testLocation: load.sampleTest.testLocation,
            reasonForTesting: load.sampleTest.reasonForTesting,
            covidVariant: load.currentState.covidVariant
        })
    })
    return array
}

export function getKhmerDate(date){
    let d = moment.parseZone(date).format("D").toString()
    let m = moment.parseZone(date).format("M") - 1
    let y = moment.parseZone(date).format("YYYY").toString()
  
    let numArr = d.split('')
    let yearArr = y.split("")
  
    let numberKh = ['','','','','','','','','','']
    let monthKh = ['','','','','','','','','','','','']
    let newArr = []
  
    let nd =['']
    for(let i=0;i<numArr.length;i++){
      if(d < 10){
        nd.push(numberKh[0])
      }
      nd.push(numberKh[numArr[i]])
      if(i===numArr.length-1){
        newArr.push(nd.join(""))
      }
    }
  
    newArr.push(''+monthKh[m])
  
    let ny =['']
    for(let i=0;i<yearArr.length;i++){
      // if(d < 10){
      //   s.push(numberKh[0])
      // }
      ny.push(numberKh[yearArr[i]])
      if(i===yearArr.length-1){
        newArr.push(ny.join(""))
      }
    }
  
    return newArr.join(" ")
  }
  
export const subQuarantionExport = (e, x) => {
    // console.log(e, "test")
    // console.log(x, "is")

    let array = []
    
    e.map(load => {
        let obj = {}
        load.quaranting.filter(e=>e.locationName===x.locationName).map(load1 =>{
            obj = {
                ...load1,
            }
        })
        array.push({
            ...load,
            ...obj,
            coorporate: obj.coorporate ? "" : ""
        })
    })
    
    return array
}

export const subHospitalExport = (e, x) => {
    let array = []
    
    e?.map(load => {
        let obj = {}
        load.hospitalizations.filter(e=>e.hospitalName===x.hospitalName).map(load1 =>{
            obj = {
                ...load1,
            }
        })
        array.push({
            ...load,
            ...obj,
            coorporate: obj.coorporate ? "" : "",
            ...load?.vaccination[load?.vaccination.length-1],
            ...load?.sampleTest[load?.sampleTest.length-1],
            vaccinated: load?.vaccination[load?.vaccination.length-1]?.times > 0 ? "" : "",
            times: " " + load?.sampleTest?.length,
            fullName: load?.lastName + " " + load?.firstName,
            date: moment.parseZone(load?.sampleTest[load?.sampleTest.length-1]?.date).format("DD.MM.YYYY"),
            result: load?.sampleTest[load?.sampleTest?.length-1]?.result ? "" : "",
            direct: obj.direct === null ? "" : "",
            district: load.district === "" ? "" : load.district,
            commune: load.commune === "" ? "" : load.commune,
            village: load.village === "" ? "" : load.village,
        })
    })

    console.log(array, e, x)
    
    return array
}

export const convertDateToKh = (e) =>{
    let day = moment(e).format("DD")
    let month = moment(e).month()
    let year = moment(e).format("YYYY")
    let numberKh = ['','','','','','','','','','']
    let monthKh = ['','','','','','','','','','','','']
    let day1 = day.charAt(0)
    let day2 = day.charAt(1)
    let year1 = year.charAt(0)
    let year2 = year.charAt(1)
    let year3 = year.charAt(2)
    let year4 = year.charAt(3)

    let khmerDate = `${numberKh[day1]+numberKh[day2]} ${monthKh[month]} ${numberKh[year1]+numberKh[year2]+numberKh[year3]+numberKh[year4]}`

    return khmerDate
}
