import React from "react";
import { Form, Input, Button, Checkbox, Row, Col,Image,message } from "antd";
import { useApolloClient } from '@apollo/client';
import { LOGIN_USER, PROFILE } from "../graphql/auth";
import { useMutation, useQuery } from "@apollo/client";
import { currentUserVar,rolesVar } from "../cache";
import { useHistory } from "react-router-dom";
import Logo from '../asset/srLogo.png'
import { convertHospitalToSelect } from "../function/fn";


export default function Login() {

    const client  = useApolloClient();

    const history = useHistory()
    // const { data: isLogin } = useQuery(PROFILE);

    const [loginUser, { error, loading }] = useMutation(LOGIN_USER, {
        onCompleted: ({loginUser}) => {
            localStorage.setItem("token", JSON.stringify(loginUser.token));
            localStorage.setItem("user", JSON.stringify(loginUser.user));
                client.resetStore()   
                currentUserVar(loginUser.user)
                message.success("")
            },
            onError: (e) => {
            //   console.log(e.message);
            message.error("!")
            },
    });
    const onFinish = (values) => {
        loginUser({
        variables: {
            username: values.username,
            password: values.password,
        },
        });
    };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  return (
 <Row>
<Col
    xs={{ span: 24}}
    md={{span: 16, offset: 4}}
    lg={{span: 12, offset: 6}}
    xl={{span: 8, offset: 8}}

>
    <div className="login-form">

        <div className="login-logo">
            <Image width={"100%"} src={Logo} preview={false} />
        </div>
        <h2 style={{ color: '#707070' }}></h2>
        <div className="line-shape"></div>
        <Form autoComplete="off"
            name="basic"
            // style={{margin:'20%'}}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
        >
            <Form.Item
                // label="Username"
                name="username"
                rules={[
                    {
                        required: true,
                        message: '!',
                    },
                ]}
            >
                <Input  placeholder="" className="login-input" />
            </Form.Item>

            <Form.Item
                // label="Password"
                name="password"
                rules={[
                    {
                        required: true,
                        message: '!',
                    },
                ]}
            >
                <Input.Password type="password" placeholder="" className="login-input" />
            </Form.Item>

            <Form.Item>
            <Button type="primary" htmlType="submit" style={{ width: "100%", height: 'auto', fontSize: 20 }}>
       
      </Button>
            </Form.Item>
        </Form>
    </div>
</Col>
</Row> 
  );
}

