import React, {useEffect, useState} from 'react'
import { Modal, Form, Row, Col, Input, DatePicker, Button, Select, message, Divider } from 'antd'
import { convertToDistrict, convertToCommune, convertToVillage, convertQurantineToSelect } from '../../../function/fn'
import { SelectTestLocation, ListSelect, ListSelectTab } from "../../../static/own-comp";
import { useMutation, useQuery } from '@apollo/client';
import { CREATE_NEW_HISTORYLOCATION } from '../../../graphql/historylocation';
import moment from 'moment';
import { GET_ALL_LOCATION } from '../../../graphql/location';
import AddLocation from '../../location/modal/addLocation';
import FamilyRelated from './familyRelated'
import WorkRelated from './workRelated'
import QuarantineRelated from './quarantineRelated'
import LocationRelated from './locationRelated'
import { UPDATE_PERSONALINFO_LOCATION, UPDATE_PERSONALINFO_QUARANTINE, UPDATE_PERSONALINFO_QUARANTINE_TAB4 } from '../../../graphql/people';
import { ALL_QUARANTINEINFO } from '../../../graphql/quarantine'
import AddQuarantine from '../../quarantine/modal/addQuarantine'
let {Option} = Select

export default function Tab4({open, setOpen, data,setRefetch, fullName, setRefetchAffected}) {
    let [form] = Form.useForm()

    const [selectLocationData,setSelectLocationData] = useState([])
    const [newLocation ,setNewLocation] = useState("")
    const [roomNumber,setRoomNumber] = useState("")

    const [locationData, setLocationData] = useState([])
    const [locationId, setLocationId] = useState("")
    const [location, setLocation] = useState({})

    const [openModal, setOpenModal] = useState(false)
    const [openFamilyRelated, setOpenFamilyRelated] = useState(false)
    const [openWorkRelated, setOpenWorkRelated] = useState(false)
    const [openQuarantineRelated, setOpenQuarantineRelated] = useState(false)
    const [openLocationRelated, setOpenLocationRelated] = useState(false)
    const [relatedType,setRelatedType] = useState("")
    const [quarantineId, setQuarantineId] = useState("")
    const [quarantine, setQuarantine] = useState({})
    useEffect(()=>{
        if(data!==undefined){
            form.setFieldsValue({
                ...data,
                // from: 
                quarantineInfo:data?.quarantineName,
                roomNumber: data.quaranting?.roomNumber,
                totalRoomate: data.quaranting?.totalRoomate
            })
            setRoomNumber(data.quaranting?.roomNumber)
        }
    },[data])

    const [updatePersonalInfo,{loading}] = useMutation(UPDATE_PERSONALINFO_LOCATION,{
        onCompleted:({updatePersonalInfo})=>{
            if(updatePersonalInfo.success===true){
                // setRefetch()
                // message.success("")
            }else{
                message.success(updatePersonalInfo.message)
            } 
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    // const { data: quarantineInfo, loading: loadingQuarantine, error } = useQuery(ALL_QUARANTINEINFO, {
    //     onCompleted: ({ allQuarantineInfos }) => {

    //     }
    // })
    // const allQuarantineInfos = quarantineInfo?.allQuarantineInfos;
    // console.log(allQuarantineInfos);
    //const { caseDataDispatch } = useContext(CaseController)
    // const [addHistoryWithin14days, { loading:createLoading}] = useMutation(CREATE_NEW_HISTORYLOCATION, {
    //     onCompleted: ({ addHistoryWithin14days }) => {
    //         // setRefetch()
    //         message.success("")
    //     },
    //     onError: (error) => {
    //         console.log(error.message)
    //     }
    // })

    // const {data:allLocation, loading,error} = useQuery(GET_ALL_LOCATION,{
    //     onCompleted:({allAffectedLocations})=>{
    //     console.log("location",allAffectedLocations)
    //     setLocationData(allAffectedLocations)
    // }})

    const [updatePeopleFromQuarantine,{loading:quarantineLoading}]=useMutation(UPDATE_PERSONALINFO_QUARANTINE,{
        onCompleted:({updatePeopleFromQuarantine})=>{
            if(updatePeopleFromQuarantine.success===true){
                setRefetch()
                message.success("")
            }else{
                message.error(updatePeopleFromQuarantine.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    const onFinish = (values) => {

        updatePersonalInfo({variables: {
            ...values,
            id:data.peopleId,
            totalCoworker:parseInt(values.totalCoworker)
            // dob: values.dob === null || values.dob=== undefined ? null : moment(values.dob),
        }});

        let quaranting = data.quaranting
        updatePeopleFromQuarantine({
            variables:{
                ...quaranting,
                roomNumber:values.roomNumber,
                totalRoomate: parseInt(values.totalRoomate),
                quarantineInfo:data.quaranting.quarantineInfo,
                quarantineInfoInnerId: data.quaranting.id,
                personalInfoId:data.peopleId
            }
        })
 
        setOpen(false)
        form.resetFields()
        // setLocationId("")
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };


    const setTestLocationFn = (e) => {

        form.setFieldsValue({
          testLocation: e,
        });
       
      };

      const addItem = () => {
        // console.log('addItem');
    
        // let isExist = selectLocationData.filter(e => e === newLocation)
    
        // if(newLocation.trim()===""){
        //   message.error("")
        //   setNewLocation("")
        //   return;
        // }
    
        // if(isExist.length > 0){
        //   message.error("")
        // }else{
          
        //   setSelectLocationData([...selectLocationData,newLocation])
        //   // form.setFieldsValue({
        //   //   testLocation: newLocation.toString(),
        //   // });
        //   setTestLocationFn(newLocation)
        //   setNewLocation("")
        // }
          
      };

      const setToQuarantineFn = (e) => {

        form.setFieldsValue({
            quarantineInfo: e
        });
        if (e === "new") {
            setOpenModal(true)
        }

        setQuarantineId(e)
    };
    const callbackQuarantine = (e) =>{
        if (e === "") {
            setQuarantineId(e)
            form.setFieldsValue({
                quarantineInfo: null
            })
        } else {
            setQuarantine(e)
            form.setFieldsValue({
                quarantineName: e.locationName
            })
        }
    }
    const setLocationTypeFn = (e) => {
        form.setFieldsValue({
            type: e
        });
    };

    const setToLocationFn = (e) => {
        form.setFieldsValue({
            affectedLocation: e
        });
        if (e === "new") {
            setOpenModal(true)
        }

        setLocationId(e)
    };

    const callbackLocation = (e) => {
        if(e === ""){
            setLocationId(e)
            form.setFieldsValue({
                affectedLocation: null
            })
        }else {
            setLocation(e)
            form.setFieldsValue({
                locationName: e.locationName
            })
        }
    }

    const handleOpenRelatedModal=e=>{
        setRelatedType(e)
        // if(e===""){
            setOpenWorkRelated(true)
        // }else if(e===""){
        //     setOpenWorkRelated()
        // }
        
    }

    return (
        <Modal
            visible={open}
            onCancel={() => setOpen(false)}
            title="."
            footer={null}
        >
            <AddLocation open={openModal} setOpen={setOpenModal} locationId={locationId} setLocationId={callbackLocation} />
            <FamilyRelated open={openFamilyRelated} setOpen={setOpenFamilyRelated} setRefetch={setRefetchAffected} data={data} fullName={fullName} type={relatedType} />
            <WorkRelated open={openWorkRelated} setOpen={setOpenWorkRelated} setRefetch={setRefetchAffected} data={data} fullName={fullName} type={relatedType} />
            <QuarantineRelated open={openQuarantineRelated} setOpen={setOpenQuarantineRelated} data={data} setRefetch={setRefetch} setRefetchAff={setRefetchAffected} fullName={fullName} roomNumber={roomNumber} type={""} />
            <LocationRelated open={openLocationRelated} setOpen={setOpenLocationRelated} setRefetch={setRefetch} data={data} />
            <AddQuarantine open={openModal} setOpen={setOpenModal} quarantineId={quarantineId} setQuarantineId={callbackQuarantine}  />

            <Form
                form={form}
                name="tab4"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{span: 24, offset: 0}}>
                        <Form.Item
                            name="currentAddress"
                            rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        style={{
                            marginBottom: 20
                        }}
                    >
                        <Button 
                                type="primary"
                                style={{
                                    width:"100%"
                                }}
                                onClick={() => setOpenFamilyRelated(true)}
                            >
                                ?
                            </Button>
                    </Col>

                    <Divider></Divider>

                    <Col xs={24}>
                        <Form.Item
                            name="workplaceInfo"
                        >
                            <Input placeholder="  " />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Form.Item
                            name="totalCoworker"
                        >
                            <Input type="number" placeholder="?" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        style={{
                            marginBottom: 20
                        }}
                    >
                        <Button 
                                type="primary"
                                style={{
                                    width:"100%"
                                }}
                                onClick={() => handleOpenRelatedModal("")}
                            >
                                 ?
                            </Button>
                    </Col>

                    <Divider></Divider>

                    <Col 
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="carPlateNumber"
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col 
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="driverName"
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col 
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="from"
                        >
                            <Input
                                placeholder="" 
                                style={{
                                    width: "100%"
                                }}/>
                        </Form.Item>
                    </Col>
                    <Col 
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="to"
                        >
                            <Input 
                                placeholder="" 
                                style={{
                                    width: "100%"
                                }}
                            />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        style={{
                            marginBottom: 20
                        }}
                    >
                        <Button 
                                type="primary"
                                style={{
                                    width:"100%"
                                }}
                                onClick={() => handleOpenRelatedModal("")}
                            >
                                ?
                            </Button>
                    </Col>

                    <Divider></Divider>

                    {/* <Col 
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="quarantineInfo"
                        >
                            
                            <ListSelect type={6} data={convertQurantineToSelect(allQuarantineInfos)} title="" setValue={setToQuarantineFn} disabled={quarantineId === "new" ? true : false} />
                        </Form.Item>
                    </Col> */}
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="quarantineInfo"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" disabled />
                            {/* <ListSelectTab disabled={true} type={6} data={convertQurantineToSelect(allQuarantineInfos)} title="" setValue={setToQuarantineFn} disabled={quarantineId === "new" ? true : false} /> */}
                        </Form.Item>
                    </Col>

                    {
                        quarantineId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="quarantineName"
                                >
                                    <Input disabled={true} style={{ backgroundColor: "white", color: "black" }} />
                                </Form.Item>
                            </Col>
                        ) : null
                    }
                    <Col 
                        xs={24}
                        md={ quarantineId === "new" ?{span: 11, offset: 0}: {span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="roomNumber"
                        >
                            <Input placeholder="" onChange={(e)=> setRoomNumber(e.target.value)} />
                        </Form.Item>
                    </Col>

                    <Col 
                        xs={24}
                        md={ quarantineId === "new" ?{span: 11, offset: 2}: {span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="totalRoomate"
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        style={{
                            marginBottom: 20
                        }}
                    >
                        <Button 
                                type="primary"
                                style={{
                                    width:"100%"
                                }}
                                onClick={() => setOpenQuarantineRelated(true)}
                            >
                                ?
                            </Button>
                    </Col>

                    <Divider></Divider>

                    <Col
                        xs={24}
                        style={{
                            marginBottom: 20
                        }}
                    >
                        <Button 
                                type="primary"
                                style={{
                                    width:"100%"
                                }}
                                onClick={() => setOpenLocationRelated(true)}
                            >
                                ?
                            </Button>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
