import React, { useContext, useState, useEffect } from 'react'
import { Row, Col, Button, Input, Table, message, Select, DatePicker, Space, Card } from 'antd'
import { PlusOutlined, EnvironmentOutlined } from '@ant-design/icons';
import { locationCol } from '../component/location/tableColumn/locationColumn'
import { useQuery, useMutation } from '@apollo/client';
import { GET_ALL_CASES, DELETE_CASE_BY_ID } from '../graphql/case'
import { GET_AFFECTED_LOCATION_PAGINATION } from '../graphql/location';
import AddLocation from '../component/location/modal/addLocation';
import EditLocation from '../component/location/modal/editLocation';
import { DELETE_AFFECTEDLOCATION_BY_ID } from '../graphql/location';
import moment from 'moment'
import ExportToExcel from "../component/export/exportToExcel";
import {PeopleList} from '../asset/exportExcel/reportExcel'
import {convertForPeopleExcel} from '../function/fn'

const { Option } = Select
const {RangePicker} = DatePicker

export default function Location() {
    //const {caseData, caseDataDispatch} = useContext(CaseController)
    // const [locationData, setLocationData] = useState([])
    const [dataEdit, setDataEdit] = useState({})

    const [openAdd, setOpenAdd] = useState(false)
    const [openEdit, setOpenEdit] = useState(false) 

    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(15)
    const [keyword, setKeyword] = useState("")

    const [ranges, setRanges] = useState({
        startDate: null,
        endDate: null,
    })

    const { data, loading, error, refetch } = useQuery(GET_AFFECTED_LOCATION_PAGINATION, {
        variables: {
            page:page,
            limit:limit,
            keyword:keyword,
            startDate: ranges.startDate,
            endDate: ranges.endDate,
        },
        // onCompleted: ({ getAffectedLocationWithPagination }) => {
        //     setLocationData(getAffectedLocationWithPagination)

        // },
        fetchPolicy:'network-only',
        nextFetchPolicy:"cache-only"
    })

    const getAffectedLocationWithPagination = data?.getAffectedLocationWithPagination

    
    const [deleteAffectedLocation, { loading: deleteLoading }] = useMutation(DELETE_AFFECTEDLOCATION_BY_ID, {
        onCompleted: ({deleteAffectedLocation}) => {
            
            if(deleteAffectedLocation.success === true){
                refetch()
                message.success("")
            }else{
                message.error("")
            }
            
        },onError:(error)=>{
            message.error(error.message)
        }
    })

    useEffect(() => {
        refetch()
    }, [page, limit, keyword, ranges])

    const callRefetch = ()=>{
        console.log("ref")
        
    }


    const handleDelete = (e) => {
        //caseDataDispatch({type: "DELETE_CASE", payload: e})
        deleteAffectedLocation({
            variables: {
                id: e
            }
        })
    }

    const handleEdit = (e) => {
        setOpenEdit(true)
        setDataEdit(e)
        // console.log(e)
    }

    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = moment(e[0])
            let endDate = moment(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })
        }
    }

    const dateHolder = ["", ""]

    return (
        <Row>
            <AddLocation open={openAdd} setRefetch={refetch} setOpen={setOpenAdd} />
            <EditLocation open={openEdit} setOpen={setOpenEdit} dataEdit={dataEdit} setRefetch={refetch} />
            <Col
                xs={24}
                style={{
                    marginBottom: 20
                }}
            >
                <Row>
                    <Col
                        xs={24}
                        md={8}
                    >
                        <Card 
                            className="location-card" 
                            title={<span><EnvironmentOutlined
                                style={{
                                    color: 'black',
                                    fontSize: 24
                                }}
                                /> /</span> }
                            style={{
                                height:'100%',
                                marginRight: 5,
                            }}
                        >
                            {/* <span style={{fontSize:40}}>200</span>  */}
                            
                            <table className="table-subHospital-info">
                                <tr>
                                    <td></td>
                                    <td>&emsp; </td>
                                    <td>0</td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>&emsp; </td>
                                    <td>0 </td>
                                </tr>
                            </table>
                        </Card>
                    </Col>
                    <Col
                        xs={24}
                        md={8}
                    >
                        <Card 
                            className="location-card" 
                            title={<span><EnvironmentOutlined 
                                style={{
                                    color: 'rgb(174, 220, 255)',
                                    fontSize: 24
                                }}
                            /> </span> }
                            style={{
                                height:'100%',
                                marginLeft: 5,
                                marginRight: 5,
                            }}
                        >
                            {/* <span style={{fontSize:40}}>200</span>  */}
                            
                            <table className="table-subHospital-info">
                                <tr>
                                    <td></td>
                                    <td>&emsp; </td>
                                    <td>0 </td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>&emsp; </td>
                                    <td>0 </td>
                                </tr>
                            </table>
                        </Card>
                    </Col>
                    <Col
                        xs={24}
                        md={8}
                    >
                        <Card 
                            className="location-card" 
                            title={<span><EnvironmentOutlined
                                    style={{
                                        color: 'red',
                                        fontSize: 24
                                    }}
                                /> </span> }
                            style={{
                                height:'100%',
                                marginLeft: 5,
                            }}
                        >
                            {/* <span style={{fontSize:40}}>200</span>  */}
                            
                            <table className="table-subHospital-info">
                                <tr>
                                    <td></td>
                                    <td>&emsp; </td>
                                    <td>0 </td>
                                </tr>
                                <tr>
                                    <td></td>
                                    <td>&emsp; </td>
                                    <td>0 </td>
                                </tr>
                            </table>
                        </Card>
                    </Col>
                </Row>
            </Col>
            <Col
                xs={24}
                md={8}
            >
                <Row>
                    <Col
                        xs={10}
                    >
                        <Button
                            type="primary"
                            onClick={() => setOpenAdd(true)}
                        >
                            
                            <PlusOutlined />
                        </Button>
                    </Col>
                    <Col
                        xs={14}
                    >
                        <Space>
                            <ExportToExcel 
                                headers={PeopleList} 
                                data={
                                []
                                } 
                                filename={"sampleTest"} 
                                title={"Export"}
                            />
                            <Select placeholder="">
                                <Option value={100}>100</Option>
                                <Option value={200}>200</Option>
                            </Select>
                        </Space>
                    </Col>
                </Row>
            </Col>
            <Col
                xs={24}
                md={16}
            >
                <Row>
                    <Col xs={24} md={8}>
                        <RangePicker 
                            // onChange={(e) => setFilterDate(e)}  
                            onChange={e => onSelectDate(e)} 
                            placeholder={dateHolder} 
                            style={{width:"100%"}} 
                        />
                    </Col>
                    <Col
                        xs={24}
                        md={{span:7, offset: 1}}
                    >
                        <Select style={{ width: "100%" }} placeholder="">
                            <Option value={true}></Option>
                            <Option value={false}></Option>
                        </Select>
                    </Col>
                    <Col
                        xs={24}
                        md={{ span: 7, offset: 1 }}
                    >
                        <Input.Search
                            onChange={(e) => setKeyword(e.target.value)}
                            placeholder="..."
                        />
                    </Col>
                </Row>
            </Col>
            <Col
                xs={24}
                style={{ marginTop: 20 }}
            >
                <Table
                    className="table-go-list"
                    columns={locationCol({ handleDelete, handleEdit, limit, page })}
                    dataSource={getAffectedLocationWithPagination?.affectedLocations}
                    rowKey={record => record.id}
                    // size="small"
                    pagination={{
                        total: getAffectedLocationWithPagination?.paginator?.totalDocs,
                        // showSizeChanger: true,
                        current: getAffectedLocationWithPagination?.paginator?.currentPage,
                        onChange:((page, pageSize) => {setPage(page);setLimit(pageSize)} )
                    }}
                    scroll={{ x: 1500 }}
                    sticky
                    loading={loading}
                />
            </Col>
        </Row>
    )
}
