import React, {useEffect, useState} from 'react'
import { Modal, Form, Row, Col, Input, DatePicker, Button, Select, message } from 'antd'
import { nationalityData, provinceData, districtData, communeData, villageData } from '../../../context/headerContext'
import { ListSelect } from '../../../static/own-comp'
import { convertToCommune, convertToDistrict, convertToVillage } from '../../../function/fn'
import Vaccine from './vaccine'
import { UPDATE_PERSONALINFO_PATIENTINFO } from '../../../graphql/people'
import moment from 'moment'
import { useMutation } from '@apollo/client'

let {Option} = Select

export default function Tab2({open, setOpen, data, setRefetch}) {
    let [form] = Form.useForm()

    const [openVaccine, setOpenVaccine] = useState(false)
    const [vaccinationData,setVaccinationData] = useState({})

    const [province, setProvince] = useState("")
    const [district, setDistrict] = useState("")
    const [commune, setCommune] = useState("")

    useEffect(()=>{
        if(data!==undefined){
            form.setFieldsValue({
                ...data,
                dob: data.dob === null || data.dob === undefined ? null : moment(data.dob),
            })

            setProvince(data.province)
            setDistrict(data.district);
            setCommune(data.commune);

            setVaccinationData({peopleId:data.peopleId,vaccination:data.vaccination})
        }
    },[data,open])

    const [updatePersonalInfo, { loading: loadingCreate, error: errorCreate }] =
    useMutation(UPDATE_PERSONALINFO_PATIENTINFO, {
      onCompleted: ({ updatePersonalInfo }) => {
        
        if(updatePersonalInfo.success===true){
            setRefetch();
            message.success("");
        }else{
            message.error(updatePersonalInfo.message)
        }
        
      },
      onError: (error) => {
        message.error(error.message);
      },
    });

    const onFinish = (values) => {
        
        updatePersonalInfo({variables: {
            ...values,
            id:data.peopleId,
            dob: values.dob === null || values.dob=== undefined ? null : moment(values.dob),
        }});
    
        setOpen(false);
        form.resetFields();
      };
    
    const onFinishFailed = (errorInfo) => {
        console.log("Failed:", errorInfo);
    };

    const setToProviceFn = (e) => {
        form.setFieldsValue({
            province: e,
            district: null,
            commune: null,
            village: null,
        });

        setProvince(e)
        setDistrict("")
        setCommune("")
    };


    const setToDistrictFn = (e) => {
        form.setFieldsValue({
            district: e,
            commune: null,
            village: null,
        });

        setDistrict(e)
        setCommune("")
    };

    const setToCommuneFn = (e) => {
        form.setFieldsValue({
            commune: e,
            village: null,
        });

        setCommune(e)
    };

    const setToVillageFn = (e) => {
        form.setFieldsValue({
            village: e
        });
    };


    return (
        <Modal
            visible={open}
            onCancel={() => setOpen(false)}
            title="."
            footer={null}
        >
            <Vaccine open={openVaccine} setOpen={setOpenVaccine} data={vaccinationData} setRefetch={setRefetch} />
            <Form
                name="tab2"
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="lastName"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="firstName"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="gender"
                        >
                            <Select placeholder="">
                                <Option value="" key=""></Option>
                                <Option value="" key=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="nationality"
                        >
                            <Select placeholder="">
                                {nationalityData.map(load => {
                                    return <Option value={load.title} key={load.title}>{load.title}</Option>
                                })}
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="dob"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker style={{width: "100%"}} placeholder=""/>
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="occupation"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="province"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={1} data={provinceData} title="/" setValue={setToProviceFn} />
                        </Form.Item>
                    </Col>

                    {province === "" ? (
                        <>
                            <Col xs={24} md={{ span: 11 }}>
                                <Form.Item
                                    name="district"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={0} data={convertToDistrict(districtData)} title="/" setValue={setToDistrictFn} disabled={province !== "" ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="commune"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToCommune(district, communeData)} title="/" setValue={setToCommuneFn} disabled={district === "" || district === null ? true : false} />
                                </Form.Item>
                            </Col>
                            <Col xs={24} md={{ span: 24 }}>
                                <Form.Item
                                    name="village"
                                    rules={[{ required: true, message: '!' }]}
                                >
                                    <ListSelect type={1} data={convertToVillage(commune, villageData)} title="" setValue={setToVillageFn} disabled={commune === "" || commune === null ? true : false} />
                                </Form.Item>
                            </Col>
                        </>
                    ) : null}

                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="social"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="idCard"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="/" />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="tel"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" addonBefore="+855" type="number" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}

                    >
                        <Button 
                                type="primary"
                                style={{
                                    width:"100%"
                                }}
                                onClick={() => setOpenVaccine(true)}
                            >
                                
                            </Button>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Button 
                                type="primary" 
                                htmlType="submit"
                                style={{
                                    width:"100%"
                                }}
                            >
                                
                            </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
