import React, { useEffect, useState } from 'react'
import { Modal, Form, Row, Col, Input, DatePicker, Button, message, Divider,Checkbox } from 'antd'
import { useMutation } from '@apollo/client';
import { UPDATE_PERSONALINFO_INTRO } from '../../../graphql/people';
import moment from 'moment';

export default function Tab1({open, setOpen, data,setRefetch}) {

    let [form] = Form.useForm();
    const [interviewed, setInterviewed] = useState(false);

    useEffect(()=>{
        if(data!==undefined){
            form.setFieldsValue({
                ...data,
                interviewedAt: data.interviewedAt === null || data.interviewedAt=== undefined ? null : moment(data.interviewedAt),
                recievedLabFormAt: data.recievedLabFormAt === null || data.recievedLabFormAt=== undefined ? null : moment(data.recievedLabFormAt),
                updateAt: data.updateAt === null || data.updateAt=== undefined ? null : moment(data.updateAt),
            })

            setInterviewed(data.interviewed)
        }
    },[data,open])

    const [updatePersonalInfo, { loading: loadingCreate, error: errorCreate }] =
    useMutation(UPDATE_PERSONALINFO_INTRO, {
      onCompleted: ({ updatePersonalInfo }) => {
        
        if(updatePersonalInfo.success===true){
            setRefetch();
            message.success("");
        }else{
            message.error(updatePersonalInfo.message)
        }
        
      },
      onError: (error) => {
        message.error(error.message);
      },
    });

    const onFinish = (values) => {
        
        updatePersonalInfo({variables: {
            ...values,
            id:data.peopleId,
            interviewedAt: values.interviewedAt === null || values.interviewedAt=== undefined ? null : moment(values.interviewedAt),
            recievedLabFormAt: values.recievedLabFormAt === null || values.recievedLabFormAt=== undefined ? null : moment(values.recievedLabFormAt),
            updateAt: values.updateAt === null || values.updateAt=== undefined ? null : moment(values.updateAt),
        }});
        
        
        setOpen(false);
        form.resetFields();
      };
    
    const onFinishFailed = (errorInfo) => {
        console.log("Failed:", errorInfo);
    };

    return (
        <Modal
            visible={open}
            onCancel={() => setOpen(false)}
            title="Intro"
            footer={null}
        >
            <Form
                name="Intro"
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="recievedLabFormAt"
                            rules={[{ required: true, message: "!" }]}
                        >
                            <DatePicker placeholder="" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="officerId"
                            rules={[{ required: true, message: "!" }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    {/* <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="interviewedAt"
                            // rules={[{ required: true, message: "!" }]}
                        >
                            <DatePicker placeholder="" style={{width: "100%"}} />
                        </Form.Item>
                    </Col> */}
                    
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="updateAt"
                            // rules={[{ required: true, message: "!" }]}
                        >
                            <DatePicker placeholder="" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>

                    <Divider></Divider>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item name="interviewed" valuePropName="checked">
                        <Checkbox onChange={(e) => setInterviewed(e.target.checked)}>
                            
                        </Checkbox>
                        </Form.Item>
                    </Col>

                    {interviewed ? (
                        <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="interviewedAt"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker
                            style={{ width: "100%" }}
                            placeholder=""
                            />
                        </Form.Item>
                        </Col>
                    ) : null}

                    <Col
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name=""
                        >
                            <Button 
                                type="primary" 
                                htmlType="submit"
                                style={{
                                    width:"100%"
                                }}
                            >
                                
                            </Button>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
