import { Space, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined,
    EyeOutlined

} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const subHospitalCol = ({handleDelete,hospitalName}) => {
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 20,
            render: (text, record, i) => (
                <Space size="middle">
                    {i += 1}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            width: 40,
            render: (text, record) => (
                <span>
                    {record?.lastName} {record?.firstName}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'gender',
            key: 'gender',
            width: 40,
            render: (text, record) => (
                <span>
                    {record?.gender}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'age',
            key: 'age',
            width: 40,
            render: (text, record) => (
                <span>
                    {record?.age}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'phone',
            key: 'phone',
            width: 40,
            render: (text, record) => (
                <span>
                    {record?.tel}
                </span>
            ),
        },

        {
            title: '',
            dataIndex: 'date_in',
            key: 'date_in',
            width: 30,
            render: (text, record) => {
                let hospitalLength = record?.hospitalizations?.length
                let hospitalization = record?.hospitalizations[hospitalLength-1]
                return(
                <Space size="middle">
                    {hospitalization.date_in === null ? "": moment.parseZone(hospitalization.date_in).format("DD MM YYYY")}
                    {/* {moment.parseZone(hospitalization?.date_in).format("DD MM YYYY")} */}
                </Space>
            )
            }
        },

        {
            title: '',
            dataIndex: 'date_out',
            key: 'date_out',
            width: 30,
            render: (text, record) => {

                // let hospitalization = record?.hospitalizations?.filter(e=> e.hospitalName === hospitalName  )
                let hospitalLength = record?.hospitalizations?.length
                let hospitalization = record?.hospitalizations[hospitalLength-1]

                return(
                <span size="middle">
                    {hospitalization.date_out === null ? "": moment.parseZone(hospitalization.date_out).format("DD MM YYYY")}
                    {/* {
                        console.log(hospitalization,hospitalName,"test")
                    } */}
                </span>
                )
            },
        },
      
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 70,
            render: (text, record) => (
                <Space size="middle">
                    {(record?.village !== "") && record?.village}
                    {record?.commune !== "" && record?.commune}
                    {record?.district !== "" && record?.district}
                    {record?.province && record?.province}
                </Space>
            ),
        },
        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 50,
            render: (text, record) => {

                let hospitalLength = record?.hospitalizations?.length
                let arr = record?.hospitalizations[hospitalLength-1]

                var dayOut = moment.parseZone(arr?.date_out).format("DD MM YYYY");
                var dayNow = moment.parseZone(new Date()).format("DD MM YYYY");
                let state ="";
                let classN = "badge-purple"
                if(arr.date_in !== null && dayOut > dayNow){
                    state = ""
                    classN= "badge-purple"
                }
                if(arr.date_out!==null &&  dayOut < dayNow){
                    state = ""
                    classN= "badge-success"
                }

                return (
                    <span className={classN}>{state}</span>
                )
            }
                // <Space size="middle">
                //     {record?.personalInfo?.currentState?.confirm ? "" : ""}
                // </Space>
         
        },
        // {
        //     title: '',
        //     dataIndex: 'relatedInfo',
        //     key: 'relatedInfo',
        //     width: 30,
        //     render: (text, record) => (
        //         <Space size="middle">
        //             {record?.personalInfo?.direct ? "":""}
        //         </Space>
        //     ),
        // },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 30,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record?.id}><EyeOutlined /></Link>
                    {/* <Popconfirm
                        title="?"
                        onConfirm={() => {handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm> */}

                </Space>
            ),
        }
    ]
    return array
}