import { Space, Popconfirm } from 'antd'
import {
    EditOutlined,
    DeleteOutlined,
    EyeOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const subQuarantineCol = ({handleDelete, handleEditSubQuarantine,limit,page}) => {
    let no = (page-1) * limit
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 50,
            render: (text, record, i) => (
                <span size="middle">
                    {no+=1}
                </span>
            ),
            
        },
        {
            title: '',
            dataIndex: 'quarantines',
            key: 'quarantines',
            width: 70,
            render: (text, record) => (
                
                <span size="middle">
                    {record?.lastName} {record?.firstName}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'gender',
            key: 'gender',
            width: 50,
            render: (text, record) => (
               
                <span size="middle">
                    {record?.gender} 
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'age',
            key: 'age',
            width: 50,
            render: (text, record) => (
                
                <span size="middle">
                    {record?.age} 
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'phone',
            key: 'phone',
            width: 70,
            render: (text, record) => (
                
                <span size="middle">
                    {record?.tel} 
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'status',
            key: 'status',
            width: 100,
            render: (text, record) => {
                let state ="";
                let classN = "badge-purple"
                var length = record?.quaranting.length;
                var arr = record?.quaranting[length-1];
                var dayOut = moment.parseZone(arr?.date_out).format("DD MM YYYY");
                var dayNow = moment.parseZone(new Date()).format("DD MM YYYY");
                // console.log(nowday,"see date new date");
                // console.log(record);
                if(arr.date_in !== null &&  dayOut < dayNow){
                    state ="";
                    classN= "badge-success";
                }
                if(arr.date_out !== null && dayOut > dayNow ){
                    state ="";
                    classN= "badge-purple"
                }
                return(
                <Space size="middle">
                    {/* {record?.currentState?.confirm ? "" : ""} */}
                    <span className={classN}>{state}</span>
                </Space>
                )
            },
        },
        {
            title: '',
            dataIndex: 'date_in',
            key: 'date_in',
            width: 70,
            render: (text, record) => {
                var length = record?.quaranting.length;
                var arr = record?.quaranting[length-1];
            return(
                <Space size="middle">
                    {moment.parseZone(arr?.date_in).format("DD MM YYYY")}
                </Space>
                )
            }
        },

        {
            title: '',
            dataIndex: 'date_out',
            key: 'date_out',
            width: 70,
            render: (text, record) => {
                var length = record?.quaranting.length;
                var arr = record?.quaranting[length-1];
            return(
            <span size="middle">
                {arr.date_out === null? "":  moment.parseZone(arr?.date_out).format("DD MM YYYY")}
            </span>
               ) 
            }      
        },
        // moment.parseZone(record?.quaranting?.date_out).format("DD MM YYYY")
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 90,
            render: (text, record) => (
                <Space size="middle">
                    {(record?.village !== "") && record?.village}
                    {record?.commune !== "" && record?.commune}
                    {record?.district !== "" && record?.district}
                    {record?.province && record?.province}
                </Space>
            ),
        },
        
        // {
        //     title: '',
        //     dataIndex: 'direct',
        //     key: 'direct',
        //     width: 50,
        //     render: (text, record) => (
        //         <Space size="middle">
        //             {record?.direct ? "" : ""}
        //         </Space>
        //     ),
        // },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 30,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record?.id}><EyeOutlined /></Link>
                    {/* <span className="link" onClick={() => handleEditSubQuarantine(record)}><EditOutlined /></span> */}
                    {/* <Popconfirm
                        title="?"
                        onConfirm={() => {handleDelete(record?.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm> */}

                </Space>
            ),
        }
    ]
    return array
}