import React, { useEffect, useState } from 'react'
import { Modal, Form, Row, Col, Button, Input, Divider, message } from 'antd'
import { useMutation } from '@apollo/client'
import { UPDATE_ADDITIONALTWO_RECORD } from '../../../graphql/dashboardAndReport'

export default function CreateAdditional({open, setOpen}) {
    let [form] = Form.useForm()

    const [updateAdditionalTwo, { loading:loadingUpdate}] = useMutation(UPDATE_ADDITIONALTWO_RECORD, {
        onCompleted: ({ updateAdditionalTwo }) => {
            if(updateAdditionalTwo.success===true){
                message.success("")
            }else{
                message.error(updateAdditionalTwo.message)
            }
            form.resetFields()
        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const onFinish = (values) => {
        console.log('Success:', values);

        let obj = {
            affectedLocation: parseFloat(values.affectedLocation),
            newAffectedLocation: parseFloat(values.newAffectedLocation),
            notClosedLocation: parseFloat(values.notClosedLocation),
            newNotClosedLocation: parseFloat(values.newNotClosedLocation),
            closedLocation: parseFloat(values.closedLocation),
            newClosedLocation: parseFloat(values.newClosedLocation),
            openedLocation: parseFloat(values.openedLocation),
            newOpenLocation: parseFloat(values.newOpenLocation),
            notCoorporateLocation: parseFloat(values.notCoorporateLocation),
            newNotCoorporateLocation: parseFloat(values.newNotCoorporateLocation),
            totalInterviewed: parseFloat(values.totalInterviewed),
            reachedPatientForInterview: parseFloat(values.reachedPatientForInterview),
            interviewChinese: parseFloat(values.interviewChinese),
            interviewChineseWomen: parseFloat(values.interviewChineseWomen),
            interviewChineseToday: parseFloat(values.interviewChineseToday),
            interViewCambodian: parseFloat(values.interViewCambodian),
            interViewCambodianWomen: parseFloat(values.interViewCambodianWomen),
            interViewCambodianToday: parseFloat(values.interViewCambodianToday),
            interviewAndAdviceAffectedPeople: parseFloat(values.interviewAndAdviceAffectedPeople),
            interviewAndAdviceAffectedPeopleWomen: parseFloat(values.interviewAndAdviceAffectedPeopleWomen),
            foundTotalAffectedLocation: parseFloat(values.foundTotalAffectedLocation),
            fullFoundTotalAffectedLocation: parseFloat(values.fullFoundTotalAffectedLocation),
            totalSampleTestLocation: parseFloat(values.totalSampleTestLocation),
            totalSampleTest: parseFloat(values.totalSampleTest),
            totalSampleTestWomen: parseFloat(values.totalSampleTestWomen)
        }

        updateAdditionalTwo({variables:obj})

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    

    return (
        <Modal
            title=""
            visible={open}
            onCancel={() => setOpen(false)}
            footer={null}
            width={800}
        >
            <Form
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                // fields={
                //     [
                //         {
                //             name
                //         }
                //     ]
                // }
            >
                <Row>
                    <Divider>II. </Divider>
                    <Col
                        xs={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="affectedLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="newAffectedLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="notClosedLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="newNotClosedLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="closedLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="newClosedLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="openedLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="newOpenLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="notCoorporateLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="newNotCoorporateLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    
                    <Divider>III. </Divider>
                    <Col
                        xs={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="totalInterviewed"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="reachedPatientForInterview"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 7, offset: 0}}
                    >
                        <Form.Item
                            name="interViewCambodian"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 8, offset: 1}}
                    >
                        <Form.Item
                            name="interViewCambodianWomen"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 7, offset: 1}}
                    >
                        <Form.Item
                            name="interViewCambodianToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 7, offset: 0}}
                    >
                        <Form.Item
                            name="interviewChinese"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 8, offset: 1}}
                    >
                        <Form.Item
                            name="interviewChineseWomen"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 7, offset: 1}}
                    >
                        <Form.Item
                            name="interviewChineseToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="interviewAndAdviceAffectedPeople"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder=" " />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="interviewAndAdviceAffectedPeopleWomen"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="foundTotalAffectedLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="fullFoundTotalAffectedLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>

                    <Divider>IV.  MOH  RRT</Divider>
                    <Col
                        xs={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="totalSampleTestLocation"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder=" " />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="totalSampleTest"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="totalSampleTestWomen"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 24, offset: 0}}
                    >
                        <Form.Item>
                            <Button
                                type="primary"
                                htmlType="submit"
                                style={{
                                    width: "100%"
                                }}
                            >
                                
                            </Button>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
