    totalGirlRecoveredToday = (arr) => {
        let total;
        arr.reduce((init,value) => {
            total = init + value.womenRecoveredToday
            return total
        },0)
        return total
    }

    totalDeaths = (arr) => {
        let total;
        arr.reduce((init,value) => {
            total = init + value.deaths
            return total
        },0)
        return total
    }

    totalGirlDeaths = (arr) => {
        let total;
        arr.reduce((init,value) => {
            total = init + value.womenDeaths
            return total
        },0)
        return total
    }

    totalDeathsToday = (arr) => {
        let total;
        arr.reduce((init,value) => {
            total = init + value.deathsToday
            return total
        },0)
        return total
    }

    totalGirlDeathsToday = (arr) => {
        let total;
        arr.reduce((init,value) => {
            total = init + value.womenDeathsToday
            return total
        },0)
        return total
    }
    
