import {gql} from '@apollo/client'

export const GET_REPORT_DATA_BY_DISTRICT = gql`
query getDataForReport($startDate:DateTime,$endDate:DateTime){
    getDataForReport(startDate:$startDate, endDate:$endDate){
             _id
          menConfirmToday
         womenConfirmToday
         menConfirm
         womenConfirm
         
         menRecoveredToday
         womenRecoveredToday
         menRecovered
         womenRecovered
  
         menDeathsToday
         womenDeathsToday
         menDeaths
         womenDeaths
  }
  }
`

export const GET_PEOPLE_CONFIRM = gql`
query getPeopeleConfirmRecoverAndDeath(
  $startDate: DateTime
  $endDate: DateTime
){
    getPeopeleConfirmRecoverAndDeath(
      startDate:$startDate,
      endDate: $endDate
    ){
      confirm{
        id
        firstName
        lastName
        age
        gender
        province
        district
        commune
        village
        other
      }
      recovered{
        id
        firstName
        lastName
        age
        gender
        province
        district
        commune
        village
        other
      }
      death{
        id
        firstName
        lastName
        age
        gender
        province
        district
        commune
        village
        other
        currentState{
          deathAt
          
        }
      }
    }
  }
`

export const GET_REPORT_DATA_BY_LOCATION = gql`
query affectedLocationReport($startDate:DateTime, $endDate:DateTime){
    affectedLocationReport(startDate:$startDate,endDate:$endDate){
        totalAffectedLocation
        totalAffectedLocationToday
        totalAffectedLocationNotClosed
        totalAffectedLocationNotClosedToday
        totalAffectedLocationOn
        closedLocation
        coorporateLocation
        coorporateLocationToday
        openedLocation
        openedLocationToday
        closedLocationToday
    }
  }
`

export const GET_REPORT_DATA_INTERVIEW = gql`
query InterViewReport($startDate:DateTime, $endDate:DateTime){
  InterViewReport(startDate:$startDate,endDate:$endDate){
      interviewTotal
      totalKhmer
      totalWomenKhmer
      totalWomenKhmerToday
      totalChina
      totalChinaToday
      totalChinaWomen
      totalSampleTestLocation
      totalSampleTest
      totalSampleTestWomen
      totalAffectedLocation
      fulltotalAffectedLocation
    }
  }
`

