import { Space } from "antd"
import { Popconfirm } from "antd"
import { DeleteOutlined } from "@ant-design/icons"

export const pageColumn = ({handleDelete}) => {

    var array = [
    {
        title: '',
        dataIndex: 'page',
        key: 'page',
        width: 20,
        render:(text,record) => {

            let pageName = ""
            switch(record.page){
                case '/sample':
                    pageName=""
                    break;
                case '/interview':
                    pageName=" COVID-19"
                    break;
                case '/hospital':
                    pageName=""
                    break;
                case '/quarantine':
                    pageName=""
                    break;
                case '/specifylocation':
                    pageName="/"
                    break;  
                case '/people':
                    pageName=""
                    break;   
                case '/reportDaily':
                    pageName=""
                    break;    
                case '/reportExcel':
                    pageName=" Excel"
                    break;
                case '/case':
                    pageName=""
                    break;
                case '/user':
                    pageName=""
                    break;
                default:
                    pageName=""
                    break;
            }
        
        return (
            <span>
                {pageName}
            </span>
        )
        }
    },
    {
        dataIndex: 'action',
        key: 'action',
        width: 20,
        render:(text,record) => (
            <Space size="middle"
            >
                <Popconfirm
                        title="?"
                        onConfirm={() => { handleDelete(record.id) }}
                        okText=""
                        cancelText=""
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                </Popconfirm>
            </Space>
        )
    }
    ]

    return array
}