import React, { useEffect, useState } from 'react'
import { Modal, Form, Row, Col, Button, Input, Divider, message } from 'antd'
import { ListSelect } from '../../../static/own-comp'
import { districForMap } from '../../../context/headerContext'
import { convertToDistrict } from '../../../function/fn'
import { useMutation, useQuery } from '@apollo/client'
import { CREATE_ADDITIONAL_RECORD, GET_ALL_ADDITIONAL_DATA, UPDATE_ADDITIONAL_RECORD } from '../../../graphql/dashboardAndReport'

export default function AddMoreData({open, setOpen}) {
    let [form] = Form.useForm()

    let distric = districForMap.filter(e => e.district !== "")

    const [additionalData, setAdditionalData] = useState({})

    const [district,setDistrict] = useState("")
    
    const { data, refetch } = useQuery(GET_ALL_ADDITIONAL_DATA, {
        variables: {
            district:district
        },
        onCompleted: ({ allAdditionalData }) => {
            // console.log(allAdditionalData)
            // setAdditionalData(allAdditionalData)
            // form.setFieldsValue(allAdditionalData)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:"cache-only"
    })

    useEffect(()=>{
        if(data?.allAdditionalData !== null && data?.allAdditionalData!== undefined){
            form.setFieldsValue(data?.allAdditionalData)
        }else{
            form.setFieldsValue({
                positive:null,
                positiveWomen:null,
                hospitalizing:null,
                hospitalizingWomen:null,
                death:null,
                deathWomen:null,
                recover:null,
                recoverWomen:null,
                sampleTest:null,
                sampleTestWomen:null,
                affected:null,
                affectedWomen:null,
            })
            
        }
    },[district,data])

    const [createAdditional, { loading}] = useMutation(CREATE_ADDITIONAL_RECORD, {
        onCompleted: ({ createAdditional }) => {
            if(createAdditional.success===true){
                message.success("")
                
            }else{
                message.error(createAdditional.message)
            }

        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const [updateAdditional, { loading:loadingUpdate}] = useMutation(UPDATE_ADDITIONAL_RECORD, {
        onCompleted: ({ updateAdditional }) => {
            if(updateAdditional.success===true){
                message.success("")
                
            }else{
                message.error(updateAdditional.message)
               
            }
            setDistrict("")
            form.resetFields()
        },
        onError: (error) => {
            message.error(error.message)
        }
    })

    const onFinish = (values) => {
        // console.log('Success:', values);

        // createAdditional({variables:{
        //     ...values,
        //     positive: parseInt(values.positive),
        //     positiveWomen: parseInt(values.positiveWomen),
        //     hospitalizing: parseInt(values.hospitalizing),
        //     hospitalizingWomen: parseInt(values.hospitalizingWomen),
        //     death: parseInt(values.death),
        //     deathWomen: parseInt(values.deathWomen),
        //     recover: parseInt(values.recover),
        //     recoverWomen: parseInt(values.recoverWomen),
        //     sampleTest: parseInt(values.sampleTest),
        //     sampleTestWomen: parseInt(values.sampleTestWomen),
        //     affected: parseInt(values.affected),
        //     affectedWomen: parseInt(values.affectedWomen),
        // } })

        updateAdditional({variables:{
            ...values,
            positive: parseInt(values.positive),
            positiveWomen: parseInt(values.positiveWomen),
            hospitalizing: parseInt(values.hospitalizing),
            hospitalizingWomen: parseInt(values.hospitalizingWomen),
            death: parseInt(values.death),
            deathWomen: parseInt(values.deathWomen),
            recover: parseInt(values.recover),
            recoverWomen: parseInt(values.recoverWomen),
            sampleTest: parseInt(values.sampleTest),
            sampleTestWomen: parseInt(values.sampleTestWomen),
            affected: parseInt(values.affected),
            affectedWomen: parseInt(values.affectedWomen),
            positiveToday: parseInt(values.positiveToday),
            positiveWomenToday: parseInt(values.positiveWomenToday),
            hospitalizingToday: parseInt(values.hospitalizingToday),
            hospitalizingWomenToday: parseInt(values.hospitalizingWomenToday),
            deathToday: parseInt(values.deathToday),
            deathWomenToday: parseInt(values.deathWomenToday),
            recoverToday: parseInt(values.recoverToday),
            recoverWomenToday: parseInt(values.recoverWomenToday),
            sampleTestToday: parseInt(values.sampleTestToday),
            sampleTestWomenToday: parseInt(values.sampleTestWomenToday),
            affectedToday: parseInt(values.affectedToday),
            affectedWomenToday: parseInt(values.affectedWomenToday)
        } })

        setOpen(false)
        form.resetFields()
        setDistrict("")
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToDirstrictFn = (e) => {
        form.setFieldsValue({
            district: e,
        });
        setDistrict(e)
    };

    return (
        <Modal
            title=""
            visible={open}
            onCancel={() => setOpen(false)}
            footer={null}
            width={600}
        >
            <Form
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                // fields={
                //     [
                //         {
                //             name
                //         }
                //     ]
                // }
            >
                <Row>
                    <Col
                        xs={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="district"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelect type={1}data={convertToDistrict(distric)} title="/" setValue={setToDirstrictFn} />
                        </Form.Item>
                    </Col>
                    <Divider></Divider>
                    <Col
                        xs={{span: 5, offset: 0}}
                    >
                        <Form.Item
                            name="positive"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="positiveWomen"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 2}}
                    >
                        <Form.Item
                            name="positiveToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="positiveWomenToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Divider></Divider>
                    <Col
                        xs={{span: 5, offset: 0}}
                    >
                        <Form.Item
                            name="hospitalizing"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="hospitalizingWomen"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 2}}
                    >
                        <Form.Item
                            name="hospitalizingToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="hospitalizingWomenToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Divider></Divider>
                    <Col
                        xs={{span: 5, offset: 0}}
                    >
                        <Form.Item
                            name="recover"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="recoverWomen"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 2}}
                    >
                        <Form.Item
                            name="recoverToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="recoverWomenToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Divider></Divider>
                    <Col
                        xs={{span: 5, offset: 0}}
                    >
                        <Form.Item
                            name="death"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="deathWomen"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 2}}
                    >
                        <Form.Item
                            name="deathToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="deathWomenToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Divider></Divider>
                    <Col
                        xs={{span: 5, offset: 0}}
                    >
                        <Form.Item
                            name="sampleTest"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="sampleTestWomen"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 2}}
                    >
                        <Form.Item
                            name="sampleTestToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="sampleTestWomenToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Divider></Divider>
                    <Col
                        xs={{span: 5, offset: 0}}
                    >
                        <Form.Item
                            name="affected"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="affectedWomen"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 2}}
                    >
                        <Form.Item
                            name="affectedToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span: 5, offset: 1}}
                    >
                        <Form.Item
                            name="affectedWomenToday"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input type="number" placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
