import React, { useEffect,useState } from 'react'
import { Form, Modal, Input, Row, Col, Button, DatePicker, message,Select } from 'antd'
import { ListSelect, ListSelectTab, SelectPeople } from '../../../static/own-comp'
import { setEditSubHospital } from '../../../function/set'
import { useMutation, useQuery } from '@apollo/client'
import { ALL_HOSPIAL_INFO, UPDATE_PERSON_BY_HOSPITALINFO } from '../../../graphql/hospital'
import moment from 'moment'
import { GET_ALL_PERSONINFO_FOR_SAMPLE_ADD, UPDATE_PERSONALINFO_HOSPITAL } from '../../../graphql/people'
import { convertHospitalToSelect, convertPersonalToSelect } from '../../../function/fn'
import AddPeople from '../../people/modal/addPeople'
import AddHospital from '../../hospital/modal/addHospital'

const {Option} = Select
export default function AddPeopleHospital({ open, setOpen, data, setRefetch }) {
    let [form] = Form.useForm()
    const [personalInfoId, setPersonalInfoId] = useState("");

    const [openPersonal, setOpenPersonal] = useState(false);
    const [personalInfoData, setPersonalInfoData] = useState({});

    const [hospitalId, setHospitalId] = useState("")
    const [hospital, setHospital] = useState({})
    const [openModal, setOpenModal] = useState(false)


    const [page,setPage] = useState(1)
    const [limit,setLimit] = useState(200)
    const [keyword, setKeyword] = useState("")
    const [personalAllData,setPersonalAllData] = useState([])

    const { data:hosData, loading: loadingHospital, error } = useQuery(ALL_HOSPIAL_INFO, {
        onCompleted: ({ allHospitalInfos }) => {

        }
    })

    const allHospitalInfos = hosData?.allHospitalInfos

    const {data:personalInfo} = useQuery(GET_ALL_PERSONINFO_FOR_SAMPLE_ADD,{
        variables:{
        page:page,
        limit:limit,
        keyword:keyword,
        startDate:null,
        endDate:null,
        currentState:null
        },
        onCompleted:({getPersonalInfoWithPagination})=>{

        setPersonalAllData(getPersonalInfoWithPagination?.personalInfos)
        }
    })
  
    useEffect(()=>{
        if(personalInfo){
        setPersonalAllData(personalInfo?.getPersonalInfoWithPagination?.personalInfos)

        }
    },[keyword,personalInfo])

    const [updatePatientFromHospital, { loading }] = useMutation(UPDATE_PERSONALINFO_HOSPITAL, {
        onCompleted: ({updatePatientFromHospital}) => {
            if(updatePatientFromHospital.success===true){
                setRefetch()
                message.success("")
                // message.error(updatePatientFromHospital.message)
            }else{
                message.error(updatePatientFromHospital.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    useEffect(() => {
        if(data!==undefined){

            let lengthHospitalization = data?.hospitalizations?.length
            let hospitalization = data?.hospitalizations

            form.setFieldsValue({
                ...data,
                date_in: hospitalization?.date_in ===null || hospitalization?.date_in ===undefined ? null : moment(hospitalization?.date_in),
                date_out: hospitalization?.date_out ===null || hospitalization?.date_out ===undefined ? null : moment(hospitalization?.date_out),
                personalInfoId:data.peopleId,
                covidVariant: hospitalization?.covidVariant,
                coorporate: hospitalization?.coorporate,
                description: hospitalization?.description,
                hospitalInfo:hospitalization?.hospitalInfo
            })
            
        }
        
    }, [data])

    const onFinish = (values) => {

        let hospitalization = data?.hospitalizations
        let hospitalIndex = allHospitalInfos.findIndex(e=> e.id === values.hospitalInfo)
        let hospitalName = allHospitalInfos[hospitalIndex].hospitalName

        updatePatientFromHospital({
            variables: {
                ...values,
                date_in: moment(values.date_in).format(),
                date_out: values.date_out === undefined || values.date_out === null ? null : moment(values.date_out).format(),
                personalInfoId: data.peopleId,
                hospitalId: hospitalization.id,
                hospitalInfo:values.hospitalInfo,
                hospitalName:hospitalName
                // lat:data.lat,
                // long:data.long
            }
        })

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToPeopleFn = (e) => {
        form.setFieldsValue({
            personalInfo: e
        });
    };

        //handle select people
    const handleSearchPeople = (e) => {
        if (e) {
        setKeyword(e)
        } else {
        // setPData([])
        setPersonalAllData([])
        }
    };

    const setToPeopleHospitalFn = (e) => {
       
        form.setFieldsValue({
            hospitalInfo: e
        });
        if (e === "new") {
            setOpenModal(true)
        }

        setHospitalId(e)
    };

    const callbackLocation = (e) => {
        
        if(e === ""){
            setHospitalId(e)
            form.setFieldsValue({
                hospitalName: null
            })
        }else {
            setHospital(e)
            form.setFieldsValue({
                hospitalName: e.hospitalName
            })
        }
    }

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            {/* <AddPeople
                open={openPersonal}
                setOpen={setOpenPersonal}
                personalInfoId={personalInfoId}
                setPersonalInfoId={callbackPersonalInfo}
                personalInfoData={personalAllData}
            /> */}
            
            <AddHospital open={openModal} setOpen={setOpenModal} hospitalId={hospitalId} setHospitalId={callbackLocation} />

            <Form
                form={form}
                name="editSubHospital"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="hospitalInfo"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <ListSelectTab type={6} data={convertHospitalToSelect(allHospitalInfos)} title="" setValue={setToPeopleHospitalFn} disabled={hospitalId === "new" ? true : false}/>
                            {/* <ListSelect type={6} data={convertHospitalToSelect(allHospitalInfos)} title="" setValue={setToPeopleHospitalFn} disabled={hospitalId === "new" ? true : false} /> */}
                        </Form.Item>
                    </Col>
                    {
                        hospitalId === "new" ? (
                            <Col xs={24} md={{ span: 11, offset: 2 }}>
                                <Form.Item
                                    name="hospitalName"
                                >
                                    <Input disabled={true} style={{ backgroundColor: "white", color: "black" }} />
                                </Form.Item>
                            </Col>
                        ) : null
                    }

                    {/* <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="personalType"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col> */}

                    {/* <Col xs={24} md={{ span: 11,offset:2}}>
                        <Form.Item
                            name="in"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col> */}

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="covidVariant"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value="COVID-19">COVID-19</Option>
                                <Option value="DELTA">DELTA</Option>
                                <Option value="ALPHA">ALPHA</Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="date_in"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="date_out"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="coorporate"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="description"
                        //rules={[{ required: true, message: '!' }]}
                        >

                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
