import { gql } from '@apollo/client'

export const GET_ALL_PERSONINFO = gql`
query getPersonalInfoWithPagination($page:Int!,$limit:Int!,$keyword:String,$currentState:String,$startDate:DateTime,$endDate:DateTime,$covidType:String){
    getPersonalInfoWithPagination(page:$page,limit:$limit,keyword:$keyword,currentState:$currentState,startDate:$startDate,endDate:$endDate,covidType:$covidType){
        personalInfos{
          id
          firstName
          lastName
          age
          gender
          tel
          nationality
          occupation
          idCard
          profileImg
          village
          commune
          district
          interviewed
          province
          englishName
          patientId
          chronic
          direct
          other
          relapse
          relapseAt
          # vaccination
          createdAt
          updatedAt
          currentState{
            confirm
            confirmedAt
            death
            deathAt
            recovered
            recoveredAt
            covidVariant
          }
          sampleTest{
            date
            testLocation
            reasonForTesting
          }
          covidVariant
        }
      paginator{
        slNo
        prev
        next
        perPage
        totalPosts
        totalPages
        currentPage
        hasPrevPage
        hasNextPage
        totalDocs
      }
    }
  }
`

export const GET_ALL_PERSONINFO_FOR_SAMPLE_ADD = gql`
query getPersonalInfoWithPagination($page:Int!,$limit:Int!,$keyword:String,$currentState:String,$startDate:DateTime,$endDate:DateTime){
    getPersonalInfoWithPagination(page:$page,limit:$limit,keyword:$keyword,currentState:$currentState,startDate:$startDate,endDate:$endDate){
        personalInfos{
          id
          firstName
          lastName
        }
      
    }
  }
`

