export const UPDATE_PERSON_BY_ID = gql`
mutation updatePersonalInfo(
  $id:ID!,
  $englishName: String,
  $patientId: String,
  $firstName: String,
  $interviewed: Boolean,
  $interviewedAt: DateTime,
  $lastName: String,
  $age: Int,
  $dob: DateTime,
  $gender: String,
  $tel: String,
  $nationality: String,
  $occupation: String,
  $idCard: String,
  $village: String,
  $commune: String,
  $district: String,
  $province: String,
  $chronic: String,
  $affectedDate: DateTime,
  $patientName: String,
  $patientCode: String,
  $relation: String,
  $direct: Boolean,
  $otherAffect: String
  $relationType:String,
  $lastTouchAt:DateTime
){
  updatePersonalInfo(updatedInfo:{
    englishName: $englishName
    patientId: $patientId
    firstName: $firstName
    interviewed: $interviewed
    interviewedAt: $interviewedAt
    lastName: $lastName
    age: $age
    dob: $dob
    gender: $gender
    tel: $tel
    nationality: $nationality
    occupation: $occupation
    idCard: $idCard
    village: $village
    commune: $commune
    district: $district
    province: $province
    chronic: $chronic
    affectedFrom:{
      affectedDate: $affectedDate
      patientName: $patientName
      patientCode: $patientCode
      relation: $relation
      direct: $direct
      otherAffect: $otherAffect
      relationType:$relationType
      lastTouchAt: $lastTouchAt
    }
  },id:$id){
    success
    message
  }
}
`;

