import React, { useEffect } from 'react'
import { Modal, Form, Input, DatePicker, Row, Col, Button, message } from 'antd'
import { useMutation } from '@apollo/client'
import { UPDATE_PERSONALINFO_CHRONIC } from '../../../graphql/people'

export default function Tab6({open, setOpen, data,setRefetch}) {
    let [form] = Form.useForm()

    useEffect(()=>{
        if(data!==undefined){
            form.setFieldsValue({
                ...data,
                chronic: data.chronic === "" ? null : data.chronic,
            })
        
        }
    },[data,open])

    const [updatePersonalInfo,{loading}] = useMutation(UPDATE_PERSONALINFO_CHRONIC,{
        onCompleted:({updatePersonalInfo})=>{
            if(updatePersonalInfo.success===true){
                setRefetch()
                message.success("")
            }else{
                message.success(updatePersonalInfo.message)
            } 
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    const onFinish = (values) => {
        
        updatePersonalInfo({variables: {
            id:data.peopleId,
            chronic:values.chronic,
        }});

    
        setOpen(false);
        form.resetFields();
      };
    
    const onFinishFailed = (errorInfo) => {
        console.log("Failed:", errorInfo);
    };


    return (
        <Modal
            visible={open}
            onCancel={() => setOpen(false)}
            title="."
            footer={null}
        >
            <Form
                name="tab6"
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="chronic"
                            rules={[{ required: true, message: "!" }]}
                        >
                            <Input
                                placeholder="" 
                            />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Button 
                                type="primary" 
                                htmlType="submit"
                                style={{
                                    width:"100%"
                                }}
                            >
                                
                            </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
