import { Button, Modal, Row, Col, Divider, DatePicker, message } from 'antd';
import React, { useRef, useState, useEffect } from 'react'
import ReactToPrint from 'react-to-print';
import DatePickerTwo from '../component/datepickertwo/datePickerTwo';
import PrintReport from '../component/print/report';
import PrintReport1 from '../component/print/report1';
import PrintReport2 from '../component/print/report2';
import PrintReport3 from '../component/print/reportPersonalInfo';
import PrintReport4 from '../component/print/report3';
import PrintReport5 from '../component/print/report4';
import PrintReport6 from '../component/print/reportSiemReap';
import moment from 'moment'

import CreateAdditional from '../component/print/modal/createAdditional'

import {useQuery} from '@apollo/client'
// import {GET_REPORT_DATA_BY_DISTRICT} from '../../graphql/report'
import {
    GET_PEOPLE_CONFIRM, 
    GET_REPORT_DATA_BY_LOCATION, 
    GET_REPORT_DATA_BY_DISTRICT,
    GET_REPORT_DATA_INTERVIEW,
    GET_REPORT_DATA_ADDITIONAL,
    GET_REPORT_DATA_ADDITIONALTWO
} from '../graphql/report'

const { RangePicker } = DatePicker

export default function Report() {
    const componentRef = useRef()
    const componentRef1 = useRef()
    const componentRef2 = useRef()
    const componentRef3 = useRef()
    const componentRef4 = useRef()
    const componentRef5 = useRef()
    const componentRef6 = useRef()
    const [openModal, setOpenModal] = useState(false)
    const [openModal1, setOpenModal1] = useState(false)
    const [openModal2, setOpenModal2] = useState(false)
    const [openModal3, setOpenModal3] = useState(false)
    const [openModal4, setOpenModal4] = useState(false)
    const [openModal5, setOpenModal5] = useState(false)
    const [openModal6, setOpenModal6] = useState(false)

    const [openAdditional, setOpenAdditional] = useState(false)

    const [ranges, setRanges] = useState({
        startDate:new Date(),
        endDate: new Date(),
    })

    const [reportDataByDistrict1, setReportDataByDistrict1] = useState([])

    //report
    const [reportDataByDistrict, setReportDataByDistrict] = useState([])
    const {data:reportData}= useQuery(GET_REPORT_DATA_BY_DISTRICT,{
        variables:{
            startDate: ranges.startDate,
            endDate: ranges.endDate
        },
        onCompleted:({getDataForReport})=>{
            setReportDataByDistrict(getDataForReport?.getDataForReport)
        }
    })

    const [reportDataByLocation, setReportDataByLocation] = useState([])
    const {data:reportDataLocation}= useQuery(GET_REPORT_DATA_BY_LOCATION,{
        variables:{
            startDate: ranges.startDate,
            endDate: ranges.endDate
        },
        onCompleted:({affectedLocationReport})=>{
            setReportDataByLocation(affectedLocationReport)
        }
    })

    const [reportPeopleConfirm, setReportPeopleConfirm] = useState([])
    const {data: reportPeopleConfirmData} = useQuery(GET_PEOPLE_CONFIRM,{
        variables:{
            startDate: ranges.startDate,
            endDate: ranges.endDate
        },
        onCompleted:({getPeopeleConfirmRecoverAndDeath})=>{
            setReportPeopleConfirm(getPeopeleConfirmRecoverAndDeath)
        }
    })

    const [reportDataByInterview, setReportDataByInterview] = useState([])
    const {data:reportInterview}= useQuery(GET_REPORT_DATA_INTERVIEW,{
        variables:{
            startDate: ranges.startDate,
            endDate: ranges.endDate
        },
        onCompleted:({InterViewReport})=>{
            console.log(InterViewReport,"test")
            setReportDataByInterview(reportInterview?.InterViewReport)
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    const [reportDataByAdditional, setReportDataByAdditional] = useState([])
    const {data:reportAdditional}= useQuery(GET_REPORT_DATA_ADDITIONAL,{
        onCompleted:({allAdditional})=>{
            setReportDataByAdditional(allAdditional)
            console.log(allAdditional, "test3")
        }
    })

    const {data:reportAdditionalTwo}= useQuery(GET_REPORT_DATA_ADDITIONALTWO,{
        onCompleted:({additionalDataTwo})=>{
            // setReportDataByAdditional(allAdditional)
            // console.log(additionalDataTwo, "two")
        }
    })

    useEffect(()=>{
        // if(reportData){
        //     setReportDataByDistrict(reportData?.getDataForReport)
        // }
 
        if(reportAdditional !== undefined && reportData !== undefined){
            let array = []

            reportData?.getDataForReport?.map(load => {
                reportAdditional?.allAdditional?.map(load1 => {
                    if(load._id === load1.district){
                        array.push({   
                            ...load, 
                            confirm: (load.menConfirm + load.womenConfirm) + load1.positive,
                            womenConfirm: load.womenConfirm + load1.positiveWomen,
                            confirmToday: (load.menConfirmToday + load.womenConfirmToday) + load1.positiveToday,
                            womenConfirmToday: load.womenConfirmToday + load1.positiveWomenToday,
                            deaths: (load.menDeaths + load.womenDeaths) + load1.death,
                            womenDeaths: load.womenDeaths + load1.deathWomen,
                            deathsToday: (load.menDeathsToday + load.womenDeathsToday) + load1.deathToday,
                            womenDeathsToday: load.womenDeathsToday + load1.deathWomenToday,
                            recovered: (load.menRecovered + load.womenRecovered) + load1.recover,
                            womenRecovered: load.womenRecovered + load1.recoverWomen,
                            recoveredToday: (load.menRecoveredToday + load.womenRecoveredToday) + load1.recoverToday,
                            womenRecoveredToday: load.womenRecoveredToday + load1.recoverWomenToday,
                        }) 
                    }
                })
            })

            setReportDataByDistrict1(array)

           
        }
    },[reportData, reportDataByAdditional])



    const onSelectDate = (e) => {
        if(e !== null){
            let startDate = new Date(e[0])
            let endDate = new Date(e[1])

            setRanges({
                startDate: startDate,
                endDate: endDate
            })

        }
    } 

    const dateHolder = ["", ""]

    return (
        <div>
            <Row>
                <CreateAdditional open={openAdditional} setOpen={setOpenAdditional} />
                <Col
                    xs={24}
                    md={24}
                    lg={24}
                >
                    <RangePicker placeholder={dateHolder} defaultValue={ranges} onChange={e => onSelectDate(e)} />
                </Col>
                <Col
                    xs={24}
                    md={24}
                    lg={12}
                >
                    <Row>
                        <Divider></Divider>
                        <Col
                            xs={{span: 24, offset: 0}}
                            className="inCol"
                        >
                            <Button
                                onClick={() => {setOpenModal(true)}}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                 
                            </Button>
                        </Col>
                        <Col
                            xs={{span: 24, offset: 0}}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal1(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                
                            </Button>
                        </Col>
                        <Divider></Divider>
                        <Col
                            xs={24}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal2(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                
                            </Button>
                        </Col>
                        <Col
                            xs={24}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal4(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                                
                            </Button>
                        </Col>
                        <Col
                            xs={24}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal5(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                
                            </Button>
                        </Col>
                        <Divider></Divider>
                        <Col
                            xs={24}
                            className="inCol"
                        >
                            <Button
                                onClick={() => setOpenModal6(true)}
                                style={{
                                    width: "100%"
                                }}
                                disabled={ranges.startDate === "" && ranges.endDate === "" ? true : false}
                            >
                                - 
                            </Button>
                        </Col>
                    </Row>
                </Col>
            </Row>



            <Modal
                width={1000}
                visible={openModal}
                footer={[
                    <Button
                        type="primary"
                        key="add"
                        onClick={() => setOpenAdditional(true)}
                    >
                        
                    </Button>,
                    <ReactToPrint
                        key="print"
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef.current}
                    />
                ]}
                onCancel={() => setOpenModal(false)}
            >
                <PrintReport ref={componentRef} date={ranges} reportDataByDistrict={reportData?.getDataForReport} dataLocation={reportDataByLocation} dataInterview={reportDataByInterview} reportDataByDistrict1={reportDataByDistrict1} reportDataAdditional={reportAdditionalTwo?.additionalDataTwo[0]} />

            </Modal>

            <Modal
                width={1100}
                visible={openModal1}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef1.current}
                    />
                ]}
                onCancel={() => setOpenModal1(false)}
            >
                <PrintReport1 ref={componentRef1} />
            </Modal>

            <Modal
                width={1100}
                visible={openModal2}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef2.current}
                    />
                ]}
                onCancel={() => setOpenModal2(false)}
            >
                <PrintReport2 ref={componentRef2} />
            </Modal>

            <Modal
                width={1100}
                visible={openModal3}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef3.current}
                    />
                ]}
                onCancel={() => setOpenModal3(false)}
            >
                <PrintReport3 ref={componentRef3} />
            </Modal>

            <Modal
                width={1100}
                visible={openModal4}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef4.current}
                    />
                ]}
                onCancel={() => setOpenModal4(false)}
            >
                <PrintReport4 ref={componentRef4} />
            </Modal>

            <Modal
                width={1100}
                visible={openModal5}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef5.current}
                    />
                ]}
                onCancel={() => setOpenModal5(false)}
            >
                <PrintReport5 ref={componentRef5} />
            </Modal>

            <Modal
                width={900}
                visible={openModal6}
                footer={[
                    <ReactToPrint
                        trigger={() => <Button type="primary" ></Button>}
                        content={() => componentRef6.current}
                    />
                ]}
                onCancel={() => setOpenModal6(false)}
            >
                <PrintReport6 ref={componentRef6} data={reportPeopleConfirm} date={ranges} />
            </Modal>
        </div>
    )
}
