import { Col, Row, Typography, Table, message, Input,List,Button,Popconfirm,DatePicker, Card, Space, Select } from 'antd'
import React, { useState, useContext, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import moment from 'moment'
import { subQuarantineCol } from './tableColumn/subQuarantineColumn'
import { 
    PlusCircleOutlined, 
    BankOutlined,
    EditOutlined, 
    SearchOutlined,
    CopyOutlined,
    DeleteOutlined,
    UsergroupAddOutlined,
    UsergroupDeleteOutlined,
    UserOutlined,
    PlusOutlined
} from '@ant-design/icons';
import EditQuarantine from './modal/editQuarantine'
import AddSubQuarantine from './modal/addSubQuarantine'
import EditSubQuarantine from './modal/editSubQuarantine'
import { useMutation, useQuery } from '@apollo/client'
import { GET_PERSON_BY_QUARANTINE } from '../../graphql/quarantine'
import { GET_QUARANTINE_BY_ID } from '../../graphql/quarantine'
import { DELETE_PERSON_BY_QUARANTINE } from '../../graphql/quarantine'
import { GET_QUARANTINE_DASHBOARD } from '../../graphql/quarantine'
import ExportToExcel from "../../component/export/exportToExcel";
import {reportSubQuarantine} from '../../asset/exportExcel/reportSiemReap'
import {convertForPeopleExcel, subQuarantionExport} from '../../function/fn'

const {Title} = Typography
