export const joinArray = (arr1, arr2) => {
  let arr = []
  arr1.map(load => {
      arr2.map(load1 => {
          if(load1.district === load._id){
              arr.push({...load, ...load1})
          }
      })
  })
return arr;
}

export const sortData = (data) => {
  let sortedData = [...data];
  sortedData.sort((a, b) => {
    if (a.confirmedCase > b.confirmedCase) {
      return -1;
    } else {
      return 1;
    }
  });
  return sortedData;
};


export const prettyPrintStat = (stat) =>
  stat ? `+${numeral(stat).format("0.0a")}` : "+0";

