import React, { useEffect } from 'react'
import { Modal, Form, Input, DatePicker, Row, Col, Button, message } from 'antd'
import moment from 'moment'
import { useMutation } from '@apollo/client'
import { UPDATE_PERSONALINFO_TRAVEL } from '../../../graphql/people'

export default function Tab5({open, setOpen, data,setRefetch}) {
    let [form] = Form.useForm()

    useEffect(()=>{
        if(data!==undefined){
            form.setFieldsValue({
                ...data,
                arriveDate: data.arriveDate === null || data.arriveDate=== undefined ? null : moment(data.arriveDate),
                leavingDate: data.leavingDate === null || data.leavingDate=== undefined ? null : moment(data.leavingDate),
            })
        
        }
    },[data,open])

    const [updateTravelOverCountryHistory,{loading}] = useMutation(UPDATE_PERSONALINFO_TRAVEL,{
        onCompleted:({updateTravelOverCountryHistory})=>{
            if(updateTravelOverCountryHistory.success===true){
                setRefetch()
                message.success("")
            }else{
                message.success(updateTravelOverCountryHistory.message)
            } 
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    const onFinish = (values) => {

        updateTravelOverCountryHistory({variables: {
            ...values,
            personalInfoId:data.peopleId,
            arriveDate: values.arriveDate === null || values.arriveDate=== undefined ? null : moment(values.arriveDate),
            leavingDate: values.leavingDate === null || values.leavingDate=== undefined ? null : moment(values.leavingDate),
        }});

    
        setOpen(false);
        form.resetFields();
    };
    
    const onFinishFailed = (errorInfo) => {
        console.log("Failed:", errorInfo);
    };

    return (
        <Modal
            visible={open}
            onCancel={() => setOpen(false)}
            footer={null}
            title="."
        >
            <Form
                name="tab5"
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="arriveDate"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker
                                placeholder="" 
                                style={{
                                    width: "100%"
                                }}
                            />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="fromCountry"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input
                                placeholder="" 
                            />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="reasonForComing"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <Input
                                placeholder="" 
                            />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="leavingDate"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker
                                placeholder="" 
                                style={{
                                    width: "100%"
                                }}
                            />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="toCountry"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <Input
                                placeholder="" 
                            />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Button 
                                type="primary" 
                                htmlType="submit"
                                style={{
                                    width:"100%"
                                }}
                            >
                                
                            </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
