import React, { useContext, useState, useEffect } from 'react'
import { Form, Modal, Input, Row, Col, Button, Select, DatePicker, message } from 'antd'
import { setEditSubQuarantine } from '../../../function/set'
import { useMutation, useQuery } from '@apollo/client'
import { UPDATE_PEOPLE_BY_QUARANTINE } from '../../../graphql/quarantine'
import moment from 'moment'
import { GET_ALL_PERSONINFO_FOR_SAMPLE_ADD } from '../../../graphql/people'
import { convertPersonalToSelect } from '../../../function/fn'
import { SelectPeople } from '../../../static/own-comp'
import AddPeople from '../../people/modal/addPeople'
const { Option } = Select

export default function EditSubQuarantine({ open, setOpen, data, quarantineId, peopleData, setRefetch, quarantineData }) {

    let [form] = Form.useForm()

    const [personalInfoId, setPersonalInfoId] = useState("");
    const [openPersonal, setOpenPersonal] = useState(false);

    const [page,setPage] = useState(1)
    const [limit,setLimit] = useState(200)
    const [keyword, setKeyword] = useState("")
    const [personalAllData,setPersonalAllData] = useState([])

    const [personalInfoData, setPersonalInfoData] = useState({});

    const {data:personalInfo} = useQuery(GET_ALL_PERSONINFO_FOR_SAMPLE_ADD,{
        variables:{
          page:page,
          limit:limit,
          keyword:keyword,
          startDate:null,
          endDate:null,
          currentState:null
        },
        onCompleted:({getPersonalInfoWithPagination})=>{
          // console.log(getPersonalInfoWithPagination)
          setPersonalAllData(getPersonalInfoWithPagination?.personalInfos)
        }
      })
      
    useEffect(()=>{
        if(personalInfo){
          setPersonalAllData(personalInfo?.getPersonalInfoWithPagination?.personalInfos)
          // console.log(personalAllData)
        }
    },[keyword,personalInfo])

    console.log(data)

    const [updatePeopleFromQuarantine,{loading}]=useMutation(UPDATE_PEOPLE_BY_QUARANTINE,{
        onCompleted:({updatePeopleFromQuarantine})=>{
            if(updatePeopleFromQuarantine.success===true){
                setRefetch()
                message.success("")
            }else{
                message.error(updatePeopleFromQuarantine.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    useEffect(() => {

        if(data!== undefined){
            form.setFieldsValue({
                ...data,
                date_in: data.date_in === null || data.date_in === undefined ? null : moment.parseZone(data.date_in),
                date_out: data.date_out === null || data.date_out === undefined ? null : moment.parseZone(data.date_out),
            })
        }
    }, [data])

    const onFinish = (values) => {
        var array = {
            ...values,
            date_in: moment(values.date_in).format(),
            date_out: values.date_out === undefined || values.date_out === null ? null : moment(values.date_out).format(),
            personalInfoId:values.personalInfoId,
            quarantineInfo:quarantineData.id,
            quarantineInfoInnerId:data.id,
            locationName: quarantineData.locationName,
            long: parseFloat(quarantineData.long),
            lat: parseFloat(quarantineData.lat),
            locationType: quarantineData.locationType
        }

        updatePeopleFromQuarantine({
            variables:array
        })

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToPersonalInfoFn = (e) => {
        form.setFieldsValue({
          personalInfo: e,
        });
        if (e === "new") {
          setOpenPersonal(true);
        }
    
        setPersonalInfoId(e);
    };

    //handle select people
    const handleSearchPeople = (e) => {
        if (e) {
        setKeyword(e)
        } else {
        // setPData([])
        setPersonalAllData([])
        }
    };

    const callbackPersonalInfo = (e) => {

        if (e === "") {
    
          // setOpen(false)
          setPersonalInfoId(e);
          form.setFieldsValue({
            personalInfo: null,
          });
        } else {
          setPersonalInfoData(e);
    
          form.setFieldsValue({
            personalInfoName: e.lastName + " " + e.firstName,
          });
        }
      };

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}

        >
            <AddPeople
                open={openPersonal}
                setOpen={setOpenPersonal}
                personalInfoId={personalInfoId}
                setPersonalInfoId={callbackPersonalInfo}
                personalInfoData={personalAllData}
            />
            <Form
                form={form}
                name="editSubQuarantine"
                // initialValues={setEditSubQuarantine(data)}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                <Col xs={24} md={
                    personalInfoId === "new"
                        ? { span: 11, offset: 0 }
                        : { span: 24, offset: 0 }
                    }>
                        <Form.Item
                            name="personalInfoId"
                            rules={[{ required: true, message: '!' }]}
                        >
                            {/* <Input placeholder="" /> */}
                            {/* <Select placeholder="" style={{ width: "100%" }} >
                                {peopleData.map((people)=>(
                                     <Option key={people?.id} value={people?.id}>{people?.lastName} {people?.firstName}</Option>
                                ))}
                               
                            </Select> */}

                            <SelectPeople data={convertPersonalToSelect(personalAllData)} title="" handleSearchPeople={handleSearchPeople} setValue={setToPersonalInfoFn} />
                        </Form.Item>
                    </Col>

                    {personalInfoId === "new" ? (
                        <Col xs={24} md={{ span: 11, offset: 2 }}>
                            <Form.Item name="personalInfoName">
                                <Input
                                disabled={true}
                                style={{ backgroundColor: "white", color: "black" }}
                                />
                            </Form.Item>
                        </Col>
                    ) : null}

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="personTypes"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="date_in"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="date_out"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="coorporate"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
