import React, {useEffect, useState} from 'react'
import { Modal, Form, Row, Col, Input, DatePicker, Button, Select, message } from 'antd'
import { convertToDistrict, convertToCommune, convertToVillage, convertQurantineToSelect, convertHospitalToSelect } from '../../../function/fn'
import { SelectTestLocation, ListSelect, ListSelectTab } from "../../../static/own-comp";
import moment from 'moment';
import { useMutation, useQuery } from '@apollo/client';
import { GET_ALL_SAMPLE_LOCATION, UPDATE_SAMPLETEST_HISTORY } from '../../../graphql/sample';
import { UPDATE_PERSONALINFO_HISTORY } from '../../../graphql/people';
import { ALL_QUARANTINEINFO } from '../../../graphql/quarantine';
import { ALL_HOSPIAL_INFO } from '../../../graphql/hospital';
import AddPeopleHospital from '../modal/addPeopleHospital';
import EditSubHospital from '../../hospital/modal/editSubHospital';
import AddPeopleQuarantine from '../modal/addPeopleQuarantine';

let {Option} = Select

export default function Tab3({open, setOpen, data, setRefetch}) {
    let [form] = Form.useForm()

    const [otherState,setOtherState] = useState("")
    const [selectLocationData,setSelectLocationData] = useState([])
    const [newLocation ,setNewLocation] = useState("")
    const [openAddPeopleHospital, setOpenAddPeopleHospital] = useState(false)
    const [openEditSub,setOpenEditSub] = useState(false)
    const [locationAllData, setLocationData] = useState([])
    const [openAddPeopleQuarantine, setOpenAddPeopleQuarantine] = useState(false)

    const [quarantineData, setQuarantineData] = useState([])
    const [hospitalData, setHospitalData] = useState([])

    // const { data:location } = useQuery(GET_ALL_LOCATION_SAMPLE)

    // const { data: hospital_quarantine, refetch: refetchHospital } = useQuery(GET_HOSPITAL_QUARANTINE_BY_PERSON, {
    //     variables: {
    //         personalId: id
    //     },
    //     onCompleted: ({ getHospitalizationByPersonalInfo }) => {
    //         // console.log(getHospitalizationByPersonalInfo)
    //         setQuarantineData(getHospitalizationByPersonalInfo?.quarantineInfo)
    //         setHospitalData(getHospitalizationByPersonalInfo?.hospitalInfo)
    //     },
    //     fetchPolicy: 'network-only',
    //     nextFetchPolicy:'cache-only'
    // })

    // const { data: quarantineInfo, loading: loadingQuarantine, error } = useQuery(ALL_QUARANTINEINFO, {
    //     onCompleted: ({ allQuarantineInfos }) => {
    //         setQuarantineData(allQuarantineInfos)
    //     }
    // })

    // useEffect(()=>{
    //     if(quarantineInfo!== undefined){
    //         setQuarantineData(quarantineInfo?.allQuarantineInfos)
    //     }
    // },[quarantineInfo])

    // const { data:hospitalInfo, loading: loadingHospital } = useQuery(ALL_HOSPIAL_INFO, {
    //     onCompleted: ({ allHospitalInfos }) => {
    //         setHospitalData(allHospitalInfos)
    //     }
    // })
    // useEffect(()=>{
    //     if(hospitalInfo!== undefined){
    //         setHospitalData(hospitalInfo?.allHospitalInfos)
    //     }
    // },[hospitalInfo])

    const { data:locationFilter } = useQuery(GET_ALL_SAMPLE_LOCATION,{
        onCompleted:({getSampleTestLocation})=>{

            setLocationData(getSampleTestLocation)
        },
        fetchPolicy:'network-only',
        nextFetchPolicy:'cache-only'
    })

    useEffect(()=>{
        if(locationAllData!==undefined){
          let newLocationAll = locationAllData.map(e => e._id ).filter(e=> e !=="").filter(e=> e !==null)
          setSelectLocationData(newLocationAll)
        }
      },[locationAllData])

    useEffect(()=>{
        if(data!==undefined){
            form.setFieldsValue({
                ...data,
                date: data?.date === null || data?.date=== undefined ? null : moment(data.date),
                resultDate: data?.resultDate === null || data?.resultDate=== undefined ? null : moment(data?.resultDate),
                symptomStart: data?.symptomStart === null || data?.symptomStart=== undefined ? null : moment(data?.symptomStart),
                // hospitalInfo:
            })

        }
    },[data,open])

    const [updatePersonalInfo, { loading: loadingCreate, error: errorCreate }] =
    useMutation(UPDATE_PERSONALINFO_HISTORY, {
      onCompleted: ({ updatePersonalInfo }) => {
        
        if(updatePersonalInfo.success===true){
            // setRefetch();
            // message.success("");
        }else{
            message.error(updatePersonalInfo.message)
        }
        
      },
      onError: (error) => {
        message.error(error.message);
      },
    });

    const [updateSampleTest,{loading}] = useMutation(UPDATE_SAMPLETEST_HISTORY,{
        onCompleted:({updateSampleTest})=>{
            if(updateSampleTest.success===true){
                setRefetch()
                message.success("")
            }else{
                message.success(updateSampleTest.message)
            } 
        }
    })

    const onFinish = (values) => {
        
        updatePersonalInfo({variables: {
            id:data.peopleId,
            // dob: values.dob === null || values.dob=== undefined ? null : moment(values.dob),
            souceOfSuspect:values.souceOfSuspect,
            relapse:values.relapse
        }});

        updateSampleTest({variables: {
            personalInfoId:data.peopleId,
            sampleTestId:data.sampleTestId,
            symptomStart: values.symptomStart === null || values.symptomStart=== undefined ? null : moment(values.symptomStart),
            resultDate: values.resultDate === null || values.resultDate=== undefined ? null : moment(values.resultDate),
            date: values.date === null || values.date=== undefined ? null : moment(values.date),
            testLocation: values.testLocation,
            reasonForTesting:values.reasonForTesting,
            symptom:values.symptom,
            result:values.result
        }});
    
        setOpen(false);
        form.resetFields();
      };
    
    const onFinishFailed = (errorInfo) => {
        console.log("Failed:", errorInfo);
    };

    const setTestLocationFn = (e) => {

        form.setFieldsValue({
          testLocation: e,
        });
       
      };

      const addItem = () => {

        let isExist = selectLocationData.filter(e => e === newLocation)
    
        if(newLocation.trim()===""){
          message.error("")
          setNewLocation("")
          return;
        }
    
        if(isExist.length > 0){
          message.error("")
        }else{
          
          setSelectLocationData([...selectLocationData,newLocation])
          // form.setFieldsValue({
          //   testLocation: newLocation.toString(),
          // });
          setTestLocationFn(newLocation)
          setNewLocation("")
        }
          
      };

      const setToQuarantineFn = (e) => {

        form.setFieldsValue({
            quarantineInfo: e
        });
    };
    const setToPeopleHospitalFn = (e) => {
       
        form.setFieldsValue({
            hospitalInfo: e
        });
       
    };

    return (
        <Modal
            visible={open}
            onCancel={() => setOpen(false)}
            title="."
            footer={null}
        >
            <AddPeopleHospital open={openAddPeopleHospital} setOpen={setOpenAddPeopleHospital} setRefetch={setRefetch} data={data} />
            <AddPeopleQuarantine open={openAddPeopleQuarantine} setOpen={setOpenAddPeopleQuarantine} setRefetch={setRefetch} data={data} />
            {/* <EditSubHospital open={openEditSub} setOpen={setOpenEditSub} data={data} setRefetch={setRefetch} /> */}
            <Form
                name="Tab3"
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                    <Form.Item
                    name="date"
                    rules={[{ required: true, message: "!" }]}
                    >
                    <DatePicker
                        placeholder=""
                        style={{ width: "100%" }}
                    />
                    </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name="reasonForTesting"
                            rules={[{ required: true, message: "!" }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }} onChange={(e) => setOtherState(e)}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value="">
                                    
                                </Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    { otherState === "" ?
                    <Col
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name="reasonForTestingOther"
                            // rules={[{ required: true, message: "!" }]}
                        >
                            
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    :null }

                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="testLocation"
                            rules={[{ required: true, message: "!" }]}
                        >
                            <SelectTestLocation title="" data={selectLocationData} setNewValue={setNewLocation} newValue={newLocation} setValue={setTestLocationFn} addItem={addItem}  />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                        name="result"
                        rules={[{ required: true, message: "!" }]}
                        >
                        {/* <Input placeholder="" /> */}
                        <Select
                            placeholder=""
                            style={{ width: "100%" }}
                        >
                            <Option value={true}></Option>
                            <Option value={false}></Option>
                        </Select>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                        name="resultDate"
                        rules={[{ required: true, message: "!" }]}
                        >
                        <DatePicker
                            placeholder=""
                            style={{ width: "100%" }}
                        />
                        </Form.Item>
                    </Col>
                    {/* <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                        name="quarantineInfo"
                        
                        >
                            
                            <ListSelectTab type={6} data={convertQurantineToSelect(quarantineData)} title="" setValue={setToQuarantineFn} />
                        </Form.Item>
                    </Col> */}
                   
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                        name="relapse"
                        rules={[{ required: true, message: "!" }]}
                        >
                            <Select
                                placeholder=""
                            >
                                <Option value={true} key={""}></Option>
                                <Option value={false} key={""}></Option>
                            </Select>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="symptom"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                        name="symptomStart"
                        // rules={[{ required: true, message: "!" }]}
                        >
                        <DatePicker
                            placeholder=""
                            style={{ width: "100%" }}
                        />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        <Form.Item
                            name="souceOfSuspect"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            <Input placeholder="" />

                                {/* <Select placeholder="" style={{ width: "100%" }}>
                                    <Option value=""></Option>
                                    <Option value=""></Option>
                                    <Option value=""></Option>
                                </Select> */}
                            
                        </Form.Item>
                    </Col>
                    <Col xs={24}
                        md={{span: 11, offset: 2}}>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        
                        <Form.Item
                            name="hospitalName"
                            // rules={[{ required: true, message: '!' }]}
                        >
                            {/* <ListSelect type={6} data={convertQurantineToSelect([])} title="" setValue={setToQuarantineFn} /> */}
                            {/* <ListSelectTab type={6} data={convertHospitalToSelect(hospitalData)} title="" setValue={setToPeopleHospitalFn} disabled={true}/> */}
                            <Input placeholder="" disabled />
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name=""
                        >
                            <Button 
                                type="primary" 
                                style={{
                                    width:"100%"
                                }}
                                onClick={()=> setOpenAddPeopleHospital(true)}
                            >
                                
                            </Button>
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 11, offset: 0}}
                    >
                        
                        <Form.Item
                        name="quarantineName"
                        
                        >
                            <Input placeholder="" disabled />
                            {/* <ListSelectTab type={6} data={convertQurantineToSelect(quarantineData)} title="" setValue={setToQuarantineFn} /> */}
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 11, offset: 2}}
                    >
                        <Form.Item
                            name=""
                        >
                            <Button 
                                type="primary" 
                                style={{
                                    width:"100%"
                                }}
                                onClick={()=> setOpenAddPeopleQuarantine(true)}
                            >
                                
                            </Button>
                        </Form.Item>
                    </Col>

                    <Col
                        xs={24}
                        md={{span: 24, offset: 0}}
                    >
                        <Form.Item
                            name=""
                        >
                            <Button 
                                type="primary" 
                                htmlType="submit"
                                style={{
                                    width:"100%"
                                }}
                            >
                                
                            </Button>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
